 ;(function (ui,$, undefined) {
	
    // Step:
    // 1.,true2,false3
    // 2.,true4,false5
    // 3.,true4,false5
    // 4.
    // 5.return false
    
    /**
     * <p></p>
     * @namespace wui
     * @class logMethodCalled
     * @constructor 
     * @param methodName {string} 
     * @param [className] {string} 
     * @param [id] {string} id
     * @example
     *  
     *   js: 
     *   
            // 
            wui.logMethodCalled( "init", "wui.date", "date");   // init ; date
     *
     */
    // ui.logMethodCalled = function(methodName, className, id){
    ui.logMethodCalled = function(params){
        var args = Array.prototype.slice.call(arguments,1);
        var setting = {
            methodName: "", 
            className: "", 
            id: "", 
            isDebug: false  // {Boolean} ,false
        }

        if(typeof(params) === "string"){
            setting.methodName = params;
            setting.className = args[0];
            setting.id = args[1];
            setting.isDebug = args[2];
        }else if(typeof(params) === "object"){
            setting = $.extend(true, {}, setting, params);
        }
        // console.info(setting);

        if(!ui.logUtilConfig.isLogMethodCalled){
            return false;
        }

        if (setting.className) {
            setting.methodName = "" + setting.className + "." + setting.methodName + " ; ";
        }else{
            setting.methodName = "" + setting.methodName + " ; ";
        }

        if(setting.id){
            setting.id = "" + setting.id;
        }else{
            setting.id = "";
        }
        var message = setting.methodName + setting.id;

        if(setting.isDebug){
            if(ui.isDebug && ui.logUtilConfig.debugConfig.isLogMethodCalled){
                if( navigator.appName === "Netscape"){
                    console.log("%c" + message, "color: #52C079");
                }else{
                    console.log(message);           
                }
            }else{
                return false;
            }
        }else{
            if(ui.logUtilConfig.isLog){
                if( navigator.appName === "Netscape"){
                    console.log("%c" + message, "color: #52C079");
                }else{
                    console.log(message);           
                }
            }else{
                return false;
            }
        }
        return true;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class logParamValue
     * @constructor 
     * @param paramName {string} 
     * @param paramValue {string} 
     * @param [methodName] {string} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.logParamValue("option", {"value": "2018-01-01"}, "wui.date");   // wui.dateoption:{"value":"2018-01-01"}

     *
     */
    // ui.logParamValue = function(paramName, paramValue, methodName) {
    ui.logParamValue = function(params) {

        var args = Array.prototype.slice.call(arguments,1);
        var setting = {
            paramName: null, 
            paramValue: "", 
            methodName: null, 
            className: "", 
            isDebug: false  // {Boolean} ,false
        }

        if(typeof(params) === "string"){
            setting.paramName = params;
            setting.paramValue = args[0];
            setting.methodName = args[1];
            setting.className = args[2];
            setting.isDebug = args[3];
        }else if(typeof(params) === "object"){
            setting = $.extend(true, {}, setting, params);
        }
        // console.info(setting);

        if(!ui.logUtilConfig.isLogParamValue){
            return false;
        }

        if (setting.className) {
            setting.methodName = setting.className + "." + setting.methodName;
        }

        if(setting.methodName){
            setting.methodName = "" + setting.methodName + "";
        }else{
            setting.methodName = "";
        }

        if(setting.paramName){
            setting.paramName = "" + setting.paramName + ":\n";
        }else{
            setting.paramName = "";
        }

        setting.isDebug = setting.isDebug || false;
        // console.info(setting);

        var message = setting.paramValue;

        if(setting.isDebug){
            if(ui.isDebug && ui.logUtilConfig.debugConfig.isLogParamValue){
                if( navigator.appName === "Netscape" ){
                    console.info("%c" + setting.methodName + setting.paramName, "color:#4B83FA", message );
                }else{
                    console.info(setting.methodName + setting.paramName, message); 
                }
            }else{
                return false;
            }
        }else{
            if(ui.logUtilConfig.isLog){
                if( navigator.appName === "Netscape" ){
                    console.info("%c" + setting.methodName + setting.paramName, "color:#4B83FA", message );
                }else{
                    console.info(setting.methodName + setting.paramName, message); 
                }
            }else{
                return false;
            }
        }
        return true;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class logMessage
     * @constructor 
     * @param message {string} 
     * @param [methodName] {string} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.logMessage( ""); // 
     *
     */
    ui.logMessage = function(message, methodName, isDebug) {
    // ui.logMessage = function(params) {

        // var args = Array.prototype.slice.call(arguments,0);
        var setting = {
            message: "",
            methodName: "",
            isDebug: false  // {Boolean} ,false
        }

        if(message){
            setting.message = message;
        }

        if(methodName){
            // setting.methodName = methodName;
            setting.methodName = "" + methodName + " ";
        }

        setting.isDebug = isDebug || false;
        // console.info(setting);

        if(!ui.logUtilConfig.isLogMessage){
            return false;
        }

        // var message = setting.message;

        if(setting.isDebug){
            if(ui.isDebug && ui.logUtilConfig.debugConfig.isLogMessage){
                console.info(":" + setting.methodName + "\n", setting.message);
            }else{
                return false;
            }
        }else{
            if(ui.logUtilConfig.isLog){
                console.info(":" + setting.methodName + "\n", setting.message);
            }else{
                return false;
            }
        }
        return true;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class logWarn
     * @constructor 
     * @param message {string} 
     * @param [methodName] {string} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.logWarn( "", "wui.date");    // wui.date:
     *
     */
    ui.logWarn = function(message, methodName, isDebug) {

        // var args = Array.prototype.slice.call(arguments, 0);
        var setting = {
            message: message,
            methodName: "",
            isDebug: false  // {Boolean} ,false
        }

        if(methodName){
            setting.methodName = methodName;
        }
        setting.isDebug = isDebug || false;
        // console.info(setting);

        if(!ui.logUtilConfig.isLogWarn){
            return false;
        }

        // if(setting.methodName !== ""){
        //     setting.methodName += ":"
        // }

        if(setting.isDebug){
            if(ui.isDebug && ui.logUtilConfig.debugConfig.isLogWarn){
                console.warn("" + setting.methodName + ":" + setting.message);
            }else{
                return false;
            }
        }else{
            if(ui.logUtilConfig.isLog){
                console.warn("" + setting.methodName + ":" + setting.message);
            }else{
                return false;
            }
        }
        return true;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class logError
     * @constructor 
     * @param error {string} 
     * @param [methodName] {string} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.logError( "", "wui.date");    // wui.date:
     *
     */
    // ui.logError = function(error, methodName) {
    ui.logError = function(error, methodName, isDebug) {

        // var args = Array.prototype.slice.call(arguments, 0);
        var setting = {
            error: error,
            methodName: "",
            isDebug: false  // {Boolean} ,false
        }

        if(methodName){
            setting.methodName = methodName;
        }
        setting.isDebug = isDebug || false;
        // console.info(setting);

        if(!ui.logUtilConfig.isLogError){
            return false;
        }

        // if(setting.methodName !== ""){
        //     setting.methodName += ":"
        // }

        if(setting.isDebug){
            if(ui.isDebug && ui.logUtilConfig.debugConfig.isLogError){
                console.error("" + setting.methodName + ":" + setting.error);
            }else{
                return false;
            }
        }else{
            if(ui.logUtilConfig.isLog){
                console.error("" + setting.methodName + ":" + setting.error);
            }else{
                return false;
            }
        }
        return true;
    }

    return ui;
})(wui || {}, libs);