            isShowCheckBox: false,

            // {Boolean} ,,false
            isChangeAllChildren: false,

            // {boolean} (Enter)
            isEnabledSearch: false,

            // {Boolean} ,true,onSelectedNode/onActivate,false,,true 
            isStrictFilter: true,

            // {Boolean} ,true
            isClickFirstNode: true,

            activeNode: "",

