            on: function(type, handler) {
                if (_widgetEventType.indexOf(type) > -1) { // WUI
                    if (typeof this.handlers[type] === 'undefined') {
                        this.handlers[type] = [];
                        this.listeners.push(type);
                    }
                    this.handlers[type].push(handler);
                }
                return this;
            },
            off: function(type, handler) {
                var _i, _j, _listeners, _handlers, _len;
                if (_widgetEventType.indexOf(type) > -1) { // WUI
                    if (this.handlers[type] instanceof Array) {
                        _handlers = this.handlers[type];
                        _listeners = this.listeners;

                        for (_i = 0, _len = _listeners.length; _i < _len; _i++) {
                            if (_listeners[_i] === type) {
                                break;
                            }
                        }

                        if (handler === undefined) {
                            this.listeners.splice(_i, 1);
                            delete this.handlers[type];
                        } else {
                            for (_j = 0, _len = _handlers.length; _j < _len; _j++) {
                                if (_handlers[_j] === handler) {
                                    break;
                                }
                            }
                            if (_j < _len) {
                                this.handlers[type].splice(_j, 1);
                                if (this.handlers[type].length == 0) {
                                    this.listeners.splice(_i, 1);
                                }
                            }
                        }
                    }
                }
                return this;
            },
            emit: function(event) {
                // console.info(event);
                if (!event.target) {
                    if (!this.$Maxcontainer) {
                        event.target = this;
                    } else {
                        event.target = this.$Maxcontainer;
                    }
                }
                if (this.handlers[event.type] instanceof Array) {
                    var handlers = this.handlers[event.type];
                    for (var i = 0, len = handlers.length; i < len; i++) {
                        handlers[i](event);
                        return true;
                    }
                }
                return false;
            },
            component: function() {
                return this;
            },
            getActiveNode: function(){
                return this._tree.getActiveNode();
            },
            activateKey: function(key, isSelected){
                
                if(!key){
                    return null;
                }
                var keyArr = key.split(","),
                    actionNode = null;

                if(keyArr.length == 1){
                    actionNode = this.$element.fancytree("getTree").activateKey(key);
                }else{
                    // key1,key1-2,key1-2-3,...
                    // ,
                    var rootNode = this._tree.rootNode;
                    var lookup = function(theNode){
                        var childNodes = theNode.children;
                        if(childNodes){
                            $.each(childNodes, function(){
                                if(this.key == keyArr[0]){
                                    if(!this.isExpanded()){
                                        this.setExpanded(true);
                                    }

                                    keyArr.splice(0, 1);
                                    if(keyArr.length == 0){
                                        // key,
                                        actionNode = this;
                                    }else if(this.hasChildren()){
                                        // ,,
                                        lookup(this);
                                    }
                                    // 
                                    return false;
                                }
                            })
                        }else{
                            return false;
                        }
                    }
                    lookup(rootNode);
                }

                if(isSelected && actionNode){
                    actionNode.setSelected(true);
                }

                return actionNode;
            },
