                    defaultName = this.defaults.name,
                    isSelected = false,
                    theNode = null;
                this._tree.visit(function(node) {
                    var keyValue = node[defaultName];
                    if (keyValue === itemName) {
                        theNode = node;
                        isSelected = node.selected;
                        return false;
                        // node.setSelected(isChecked);
                        // isExist = true;
                        // return false;
                    }
                });

                if (callback && typeof(callback) === "function") {
                    callback(isSelected, theNode);
                }

                return isSelected;
