            /*
             * 
             * @param  {JSON} filterParams
             * @param  {String} filterParams.filterMode ,:"nodes"() | "branches"(),"branches"
             * @param  {Function | String} filterParams.filter :
             * @param  {JSON} filterParams.options ,filter,filter
             * @return {Number} 
             */
            filter: function(filterParams) {
                var self = this,
                    params = this._opt;

                filterParams = filterParams || {};
                var filters = {
                    filterMode: "branches",
                    filter: "",
                    options: {},
                    isResetCheckbox: true
                }

                filterParams = $.extend(true, {}, filters, filterParams);
                // console.info(filterParams);

                // 
                try {
                    if ($.ui.fancytree._extensions["filter"]) {
                        var filterNumber = 0;
                        if (filterParams.filterMode == "nodes") {
                            filterNumber = this._tree.filterNodes(filterParams.filter, filterParams.options);
                        } else if (filterParams.filterMode == "branches") {
                            filterNumber = this._tree.filterBranches(filterParams.filter, filterParams.options);
                        } else {
                            return false;
                        }

                        this._tree.visit(function(node) {
                            if (node.isSelected() && filterParams.isResetCheckbox) {
                                node.setSelected(false);
                            }

                            if (params.isStrictFilter) {
                                var isUnselectable = !(node.isMatched());
                                node.unselectable = isUnselectable;
                                if (isUnselectable) {
                                    $(node.span).addClass("fancytree-unselectable");
                                } else {
                                    $(node.span).removeClass("fancytree-unselectable");
                                }
                            }

                        });
                        return filterNumber;
                    } else {
                        ui.logWarn(" tree filter", FRAME_NAME + "." + _widgetName, true);
                    }
                } catch (err) {
                    ui.logWarn(err, FRAME_NAME + "." + _widgetName, true);
                    // ui.logWarn(" tree :jquery.fancytree.filter.js");
                }
            },
            select: function(data) {
                var _target = this.$element[0];
                if (data != undefined) {
                    if (typeof data === "string") {
                        if (data != "") {
                            var arr = data.split(",");
                            for (var i = 0; i < arr.length; i++)
                                this._setSelected(arr[i]);
                        } else {
                            // ui.logMessage("", "wui.tree", true);
                            if (this.listeners.indexOf('itemSelectError') > -1) {
                                if (!this.emit({ type: 'itemSelectError', target: _target, item: data, code: 6, message: "" })) return;
                            }
                            return false;
                        }

                    } else if (ui.isArray(data)) {
                        if (data.length > 0) {
                            for (var i = 0; i < data.length; i++)
                                this._setSelected(data[i]);
                        } else {
                            // ui.logMessage("", "wui.tree", true);
                            if (this.listeners.indexOf('itemSelectError') > -1) {
                                if (!this.emit({ type: 'itemSelectError', target: _target, item: data, code: 6, message: "" })) return;
                            }
                            return false;
                        }

                    } else if (typeof(data) === "object") {
                        if (data != null) {
                            this._setSelected(data);
                        } else {
                            if (this.listeners.indexOf('itemSelectError') > -1) {
                                if (!this.emit({ type: 'itemSelectError', target: _target, item: data, code: 6, message: "" })) return;
                            }
                            return false;
                        }

                    } else {
                        if (this.listeners.indexOf('itemSelectError') > -1) {
                            if (!this.emit({ type: 'itemSelectError', target: _target, item: data, code: 6, message: "" })) return;
                        }
                        return false;
                    }
                } else {
                    if (this.listeners.indexOf('itemSelectError') > -1) {
                        if (!this.emit({ type: 'itemSelectError', target: _target, item: data, code: 6, message: "" })) return;
                    }
                }

                return true;
            },
            unselect: function(data) {
                var _target = this.$element[0];
                if (data != undefined) {
                    if (typeof data === "string") {
                        if (data != "") {
                            var arr = data.split(",");
                            for (var i = 0; i < arr.length; i++)
                                this._setUnselected(arr[i]);
                        } else {
                            if (this.listeners.indexOf('itemUnselectError') > -1) {
                                if (!this.emit({ type: 'itemUnselectError', target: _target, item: data, code: 6, message: "" })) return;
                            }
                            return false;
                        }

                    } else if (ui.isArray(data)) {
                        if (data.length > 0) {
                            for (var i = 0; i < data.length; i++)
                                this._setUnselected(data[i]);
                        } else {
                            if (this.listeners.indexOf('itemUnselectError') > -1) {
                                if (!this.emit({ type: 'itemUnselectError', target: _target, item: data, code: 6, message: "" })) return;
                            }
                            return false;
                        }

                    } else if (typeof data === "object" && data != null) {
                        if (data != null) {
                            this._setUnselected(data);
                        } else {
                            if (this.listeners.indexOf('itemUnselectError') > -1) {
                                if (!this.emit({ type: 'itemUnselectError', target: _target, item: data, code: 6, message: "" })) return;
                            }
                            return false;
                        }
                    } else {
                        if (this.listeners.indexOf('itemUnselectError') > -1) {
                            if (!this.emit({ type: 'itemUnselectError', target: _target, item: data, code: 6, message: "" })) return;
                        }
                        return false;
                    }

                    return true;
                }
            },
