    // ui.getAjax = function(url, data, onSuccess, onError, async, dataType){
    ui.getAjax = function(param){
        ui.logUtilConfig.debugLog.methodName = "getAjax";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("getAjax", FRAME_NAME);
        
        var args = Array.prototype.slice.call(arguments,1);
        var params = {};
        if(typeof(param) === "string"){
            params.url = param;
            params.data = args[0];
            params.onSuccess = args[1];
            params.onError = args[2];
            params.async = args[3];
            params.dataType = args[4];
            params.contentType = args[5];
            params.onBeforeSend = args[6];
            params.onComplete = args[7];
            params.isAddRandomCode = args[8];
        }else if(param && typeof(param) === "object"){
            params = $.extend(true, {}, params, param);
            // params.url = param.url;
            // params.data = param.data;
            // params.onSuccess = param.onSuccess || param.onSuccess;
            // params.onError = param.onError || param.onError;
            // params.async = param.async;
            // params.dataType = param.dataType;
            // params.method = param.method;
            // params.contentType = param.contentType;
            // params.onBeforeSend = param.onBeforeSend;
            // params.onComplete = param.onComplete;
        }
        
        if(!params.url){
            ui.logError("", FRAME_NAME+".getAjax", true);
            return false;
        }

        params.method = "get";
        ui.ajax(params);
    }

