    /**
     * <p></p>
     * @namespace wui
     * @class submitByForm
     * @constructor 
     * @param args {Object} ajax
     * @param args.url {string} url
     * @param args.data {json} 
     * @param args.method {string} "post"
     * @example
     *  
     *   js: 
     *   
            // 
            wui.submitByForm({
                url: "http://xxx/server.aspx", 
                data: {},
                method: "post"
            })
     *
     */
    ui.submitByForm = function(param){
        ui.logUtilConfig.debugLog.methodName = "submitByForm";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("submitByForm", FRAME_NAME);
        // console.info("123")
        var url, data, onSuccess, onError, method;
        if(param && typeof(param) === "object"){
            url = param.url;
            data = param.data;
            onSuccess = param.onSuccess;
            onError = param.onError;
            method = param.method;
        }else{
            return false;
        }

        method = method || "post";// jsonget
        data = data || {};

        if(!url){
            ui.logError("url", FRAME_NAME+".submitByForm", true);
            return;
        }
        if(!ui.isJson(data)){
            ui.logError("datajson", FRAME_NAME+".submitByForm", true);
            return;
        }
        var form = $("<form></form>").attr("action", url).attr("method", method);
        for(var key in data){
            form.append($("<input></input>").attr({
                "type": "hidden",
                "name": key
            }).val(data[key]));
        }
        // console.info(form);
        form.appendTo('body').submit().remove();
    }
    
    return ui;
