/**
 * @module Method
 */

 ;(function (ui, $, undefined) {

    /**
     * <p>:</p>
     * @namespace wui
     * @class setTimeout
     * @constructor 
     * @param onDone {Function} 
     * @param [timeout] {Number} ,"ms",1000,1000ms onDone 
     * @return {Number} wui.clearTimeout()
     * @example
     *  
     *   js: 
     *
            var onDo = function(){
                wui.logMessage(new Date());
            }
            // 1800 onDo 
            var timeoutID = wui.setTimeout(onDo, 1800);   // 103()
     *
     */
    ui.setTimeout = function(onDone, timeout){
        timeout = timeout || 1000;
        return setTimeout(onDone, timeout);
    }

    /**
     * <p></p>
     * @namespace wui
     * @class clearTimeout
     * @constructor 
     * @param timeoutID {Number}  wui.setTimeout()  wui.setTimeout() 
     * @example
     *  
     *   js: 
     *   
            wui.clearTimeout(103);
     *
     */
    ui.clearTimeout = function(timeoutID){
        return clearTimeout(timeoutID)
    }

    /**
     * <p>:</p>
     * @namespace wui
     * @class setInterval
     * @constructor 
     * @param onDone {Function} 
     * @param [timeout] {Number} ,"ms",1000,1000ms onDone ,wui.clearInterval()
     * @return {Number} wui.clearInterval()
     * @example
     *  
     *   js: 
     *
            var onDo = function(){
                wui.logMessage(new Date());
            }
            // 1800 onDo 
            var timer = wui.setInterval(onDo, 1800);   // 104()
     *
     */
    ui.setInterval = function(onDone, cycle){
        cycle = cycle || 1000;
        return setInterval(onDone, cycle);
    }

    /**
     * <p></p>
     * @namespace wui
     * @class clearInterval
     * @constructor 
     * @param timerID {Number}  wui.setInterval()  wui.setInterval() 
     * @example
     *  
     *   js: 
     *   
            wui.clearInterval(104);
     *
     */
    ui.clearInterval = function(timerID){
        return clearInterval(timerID);
    }

    return ui;
})(wui || {}, libs);