				this.pluginName = _widgetName;	//
	            this.hasDom = false; 		//dom
				this.element = element;
	            this.$element = $("#" + element);
				this.defaults = defaults;	// 
				this._option = opt;		// 
				// this.initDom = $("#" + element).prop("outerHTML");
				// this._opt = $.extend(false, {}, config, options);	//
