            _createColModel: function(colConfig){
                var that = this,
                    // Grid
                    defaultColConfig = ui.deepCopy(that.defaults.defaultColConfig);
            
                var colModel = $.extend(true, {}, defaultColConfig, colConfig );
                // console.info(colModel);
                if(colModel.hidedlg){
                    colModel.hidden = true;
                }

                if(!ui.isEmptyType(that.defaults.mergerRowsField)){
                    if(that.defaults.mergerRowsField.indexOf(colModel.name) > -1){
                        colModel.cellattr = function(rowId, value, rowObject, colModel, arrData) {
                            //
                            return 'id=\'' + colModel.name + rowId + "\'";
                        }
                    }
                }

                colModel = this._initFormatter(colModel);

                // 
                // formatter: ""text""number""date"/"checkbox""select"/"selectbox"
                // formatter: "text""number""date"/"checkbox""text"/"text"
                // wstype: "text""number""date"/"select"/"selectbox"
                if(!colModel.search){
                    colModel.searchoptions = {};
                }

                // if (colModel.stype == "text" && colModel.formatter == "") {
                //     colModel.formatter = function(cellValue, options, rowObject){

                //         if(cellValue === null || cellValue == undefined || cellValue == ""){
                //             return self.defaults.cellValueEmpty;
                //         }else{
                //             return cellValue;
                //         }
                //     }
                // }

                return colModel;
            },
            _initColModel: function(){
                var self = this, colModels = this.defaults.colModels;
                // Grid
                for (var i = 0; i < colModels.length; i++) {
                    var colConfig = colModels[i];
                    
                    var colModel = this._createColModel(colConfig);

                    self.colModel.push(colModel);
                }
                // console.info(self.colModel);
                return self.colModel;
            },
