            /*
             * filter
             * @param  {Array} colFilters []
             * @param  {[type]} groupOp    [,"AND"]
             * @return {JSON}            [filter]
             */
            _buildFilters: function(colFilters, groupOp){
                var self = this, $selector = this.$element;
                groupOp = groupOp || self._opt.groupOp;
                var nm, sop, type, ruleGroup = "{\"groupOp\":\"" + groupOp + "\",\"rules\":[", gi=0, so;

                $.each(colFilters,function(i, rule){
                    // console.info(colFilters);
                    if(typeof(rule) == "string"){
                        // console.info(rule);
                        if (gi > 0) {
                            ruleGroup += ",";
                        }
                        ruleGroup += rule;
                        gi++;
                    }else{
                        nm = this.name || this.field || this.index;
                        sop = this.searchoptions || {};
                        // so  = p.defaultSearch ? p.defaultSearch : (sop.sopt) ? sop.sopt[0] : p.defaultSearch;
                        // so = this.sopt ? this.sopt: (p.defaultSearch ? p.defaultSearch : sop.sopt[0]);
                        so = (this.sopt || this.op) ? (this.sopt || this.op): (sop.sopt ? sop.sopt[0] : self.defaults.defaultSearch);
                        type = this.type || 'text';
                        if (this.data) {
                            if (gi > 0) {ruleGroup += ",";}
                            ruleGroup += "{\"field\":\"" + nm + "\",";
                            ruleGroup += "\"op\":\"" + so + "\",";
                            ruleGroup += "\"type\":\"" + type + "\",";
                            ruleGroup += "\"data\":\"" + this.data.replace(/\\/g,'\\\\').replace(/\"/g,'\\"') + "\"}";
                            gi++;
                        }
                    }   
                });         
                ruleGroup += "]}";
                // console.info(ruleGroup);

                // var postData = $selector.jqGrid("getGridParam", "postData");
                // $.extend(postData,{ filters:ruleGroup });
                // $.each(['searchField', 'searchString', 'searchOper'], function(i, n){
                //     if(postData.hasOwnProperty(n)) { delete postData[n];}
                // });

                // //  
                // $selector.jqGrid("setGridParam",{page:1}).trigger("reloadGrid");
                // console.info({filters:ruleGroup});
                return ruleGroup;
            },
