            _hackHeight: function() {
                var self = this, element = this.element;

                $('#' + element + '_frozen tr').slice(1).each(function() {
             
                    var rowId = $(this).attr('id');
             
                    var frozenTdHeight = parseFloat($('td:first', this).height());
                    var normalHeight = parseFloat($('#' + element + ' #' + $(this).attr('id')).find('td:first').height());
             
                    // hack
                    if (frozenTdHeight < normalHeight) {
             
                        $('td', this).each(function() {
             
                            /*
                             hack
                             */
                            var space = 0; // opera0
                            if (ui.CONSTANTS.isChrome()) {
                                space = 0.6;
                            } else if (ui.CONSTANTS.isIE()) {
                                space = -0.2;
                            } else if (ui.CONSTANTS.isMozila()) {
                                space = 0.5;
                            }
             
                            if (!$(this).attr('style') || $(this).attr('style').indexOf('height:') == -1) {
                                $(this).attr('style', $(this).attr('style') + ";height:" + (normalHeight + space) + "px !important");
                            }
                        });
                    }
                });
            },
