			// 
			deleteSelectedRows: function(param, isLocal){
				var self = this, params = this._opt, $selector = this.$element;
                
				var rowDatas = [], selectedIDs = $selector.getGridParam("selarrrow");
				if (selectedIDs && selectedIDs.length > 0) {

                    if(!isLocal){
                        var successCallback = param.onSuccess || param.successCallback,
                            errorCallback = param.onError || param.errorCallback;      
                        
                        if(!param.url){
                            ui.errorNotice("url");
                            return false;
                        }

                        var onSuccess = function(res){
                            if(successCallback && (typeof successCallback === "function")){
                                successCallback(res);
                            }else{
                                self.refreshData();
                            }
                            // console.info(res);
                            ui.deleteDialog('dialog');
                        }
                        var onError = function(res){
                            if(errorCallback && (typeof errorCallback === "function")){
                                errorCallback(res);
                            }
                            // console.info(res);
                            ui.deleteDialog('dialog');
                        }
                        var len = selectedIDs.length;
                        var content = '' + len + ',';
                        self.defaults.windowModal.wui.confirm(content, {title:''}, function() {
                            // deleteCallback(ids);
                            var urlParam = {
                                "rowids": selectedIDs.join(",")
                            };
                            // data[keyName] = selectedIDs.join(",");
                            var deleteUrl = ui.buildUrl(param.url, urlParam);
                            ui.ajax({
                                method : param.method,
                                url : deleteUrl,
                                data : param.data,
                                dataType : param.dataType,
                                contentType : param.contentType,
                                onSuccess: onSuccess,
                                onError: onError
                            })
                        });
                    }else{
                        var len = selectedIDs.length;
                        var content = '' + len + ',';
                        self.defaults.windowModal.wui.confirm(content, {title:''}, function() {
                            for(var rowIndex = 0; rowIndex < len; rowIndex++){
                                // ,0
                                $selector.jqGrid("delRowData", selectedIDs[0]);
                            }
                            ui.deleteDialog('dialog');
                        });
                            
                    }
				}else{
					ui.warnNotice("");
				}		
			},
			// 
			showFilterSearch: function(){
				var self = this, params = this._opt, selector = this.element;
				var gridTableId = "#gview_" + selector;
				var searchLine = null,
					gridDiv = $(gridTableId + ' .ui-jqgrid-bdiv'),
					searchLineHei = null,
					gridDivHei = null;

                if($(gridTableId + ' .ui-search-toolbar').length == 0){
                    // 
                    this.$element.jqGrid('filterToolbar', {
                        //stringResult : true,
                        searchOnEnter: true,
                        searchOperators:true
                    });

                    $(gridTableId + ' .ui-search-toolbar').hide();
                }

                searchLine = $(gridTableId + ' .ui-search-toolbar');
                gridDiv = $(gridTableId + ' .ui-jqgrid-bdiv');
                searchLineHei = searchLine.outerHeight();
                gridDivHei = gridDiv.outerHeight();
                // console.error(searchLine.is(':hidden'))
					
				if(searchLine.is(':hidden')){
					searchLine.show();
					var gridDivHei = gridDivHei - searchLineHei;
					gridDiv.height(gridDivHei);
				}else{
					searchLine.hide();
					var gridDivHei = gridDivHei + searchLineHei;
					gridDiv.height(gridDivHei);
				}
				
				self._gridplueFrozen();
			},
