			_getSopt: function(wstype){
				wstype = wstype || "text";

				var sopt;
				switch (wstype) {
                    // 
                    case "text":
                    case "string":
                        sopt = ['cn', 'eq', 'ne', 'bw', 'bn', 'ew', 'en', 'nc', 'nu', 'nn', 'in', 'ni'];
                        break;
                    // 
                    case "boolean":
                        sopt = ['eq', 'ne'];
                        break;
                    // 
                    case "date":
                        sopt = ['eq','ne', 'lt', 'le', 'gt', 'ge'];
                        break;
                    // 
                    case "dateRange":
                        // sopt = ['cn', 'nc'];
                        sopt = ['ge', 'le'];
                        break;
                    // 
                    case "num" :
                    case "number" :
                        sopt = ['eq','ne', 'lt', 'le', 'gt', 'ge', 'nu', 'nn', 'in', 'ni'];
                        break;
                    // 
                    case "numRange":
                        // sopt = ['cn', 'nc'];
                        sopt = ['ge', 'le'];
                        break;
                    // 
                    case "int" :
                    case "integer":
                        sopt = ['eq','ne', 'lt', 'le', 'gt', 'ge', 'nu', 'nn', 'in', 'ni'];
                        break;
                    // 
                    case "intRange":
                        // sopt = ['cn', 'nc'];
                        sopt = ['ge', 'le'];
                        break;
                    // 
                    case "double":
                        sopt = ['eq','ne', 'lt', 'le', 'gt', 'ge', 'nu', 'nn', 'in', 'ni'];
                        break;
                    // 
                    case "doubleRange":
                        // sopt = ['cn', 'nc'];
                        sopt = ['ge', 'le'];
                        break;
                    // 
                    case "checkbox":
                        sopt = ['eq'];
                        break;
                    // 
                    case "select":
                    // 
                    case "dictCode":
                        sopt = ['eq'];
                        break;
                    // wui
                    case "selectbox":
                    case "selectBox":
                        sopt = ['eq', 'cn', 'nc'];
                        break;
                    // 
                    case "choosebox" :
                    case "chooseBox" :
                        sopt = ['eq', 'cn', 'nc'];
                        break;
                    default: 
                        // 
                        sopt = ['cn'];
                        break;
				}
				return sopt;
			},
            /*
             * Grid
             * @param  {JSON} postSearchJson [JSON,: {"nameKeyWord": "", "enable": "1"}]
             * @param  {Array} colFilters     [,: [{"field": "enable", op: "cn", "data": "1", "type": "text"},...]]
             * @param  {String} searchUrl      []
             * @return NULL
             */
			searchGrid: function(colFilters, postSearchJson, searchUrl, groupOp) {
                ui.logParamValue({
                    paramName: "colFilters",
                    paramValue: colFilters, 
                    methodName: "grid.searchGrid", 
                    className: FRAME_NAME, 
                    isDebug: true
                });
                ui.logParamValue({
                    paramName: "postSearchJson",
                    paramValue: postSearchJson, 
                    methodName: "grid.searchGrid", 
                    className: FRAME_NAME, 
                    isDebug: true
                });
				var self = this, params = this._opt, $selector = this.$element;
                var newPostData = {};
                colFilters = colFilters || null;
                postSearchJson = postSearchJson || null;
                groupOp = groupOp || null;

	         	// postSearchJson = postSearchJson || {};

                var oldSearchUrl = $selector.jqGrid("getGridParam", "url");
                if(searchUrl)  {
                    $selector.jqGrid("setGridParam",{url: searchUrl});
                }

                if(postSearchJson && ui.isJson(postSearchJson)){
                    newPostData = postSearchJson;
                }

                if(colFilters && ui.isArray(colFilters)){
                    var ruleGroup = self._buildFilters(colFilters, groupOp);
                    newPostData.filters = ruleGroup;
                }

                var postData = $selector.jqGrid("getGridParam", "postData");
                // setGridParampostdatapostData
                $.each(postData, function (k, v) {  
                    delete postData[k];  
                });

                if(params.onBeforeSearchGrid && typeof(params.onBeforeSearchGrid) === "function"){
                    var returnPostData = params.onBeforeSearchGrid.call(this.$element.get(0), newPostData);
                    if(returnPostData && ui.isJson(returnPostData)){
                        // console.info(returnPostData);
                        newPostData = returnPostData;
                    }
                    // newPostData = params.onBeforeSearchGrid.call(this.$element.get(0), newPostData);
                }

                //  
                $selector.jqGrid("setGridParam", {
                    postData: newPostData, 
                    page:1
                }).trigger("reloadGrid");

                $selector.jqGrid("setGridParam",{
                    url: oldSearchUrl
                });
			},
