        // @param funcName {string} 
        // @param params {Array} 
        // @return 
		function _executeMethod(funcName, params){
			// console.info(element);
			// console.info(_pluginDataName);
			// console.info($.data(document.getElementById(element), _pluginDataName));
			var data = $.data(document.getElementById(element), _pluginDataName);
			if(!data){
				ui.logError("wui.grid ", FRAME_NAME + "." + _widgetName, true);
				return;
			}

			var args = Array.prototype.slice.call(arguments,1);

			if(typeof (data[funcName]) === "function"){
				return data[funcName].apply(data, args);
			}else{
				ui.logWarn('wui.grid  ' + funcName + '() ', FRAME_NAME + "." + _widgetName, true);
			}
		}

