	var defaultValidateStyle = function(error, theElement, tipSetting){
		tipSetting = tipSetting || {};
    	if(theElement.attr("ui-type")){
    		var widgetName = theElement.attr("ui-type");
				widgetId = theElement.attr("id") || theElement.attr("name");

			if(widgetName === "file" || widgetName === "img"){
				widgetId = theElement.parent().attr("_fileId")
			}

			if(wui && wui[widgetName]){
				var widgetValue = wui[widgetName](widgetId).get("value");
				// console.info(widgetValue);
				if(ui.isArray(widgetValue) && widgetValue.length > 0){
					return true;
				}else if(typeof(widgetValue) === "string" && widgetValue !== ""){
                    return true;
                }
			}
            // console.info(theElement.val())
			error.hide();
            switch (widgetName) {
                case 'file': // 
                	// error.insertAfter(theElement);
                    ui.tips(error[0].innerHTML, theElement.parent(), tipSetting);
                    break;
                case 'date': // 
                    // error.insertAfter(theElement);
                    ui.tips(error[0].innerHTML, theElement, tipSetting);
                    break;
                case 'img': // 
                    // error.insertAfter(theElement);
                    ui.tips(error[0].innerHTML, theElement.parent(), tipSetting);
                    break;
                case 'opinionBox':
                    ui.tips(error[0].innerHTML, theElement.next().children("textarea"), tipSetting);
                	break;
                default:
                    // error.insertAfter(theElement.parent());
                	ui.tips(error[0].innerHTML, theElement.parent(), tipSetting);
            }
    	}else{
            // console.error(theElement);
    		ui.tips(error[0].innerHTML, theElement, tipSetting);
    	}
	}

	var labelValidateStyle = function(error, theElement, tipSetting){
		if(theElement.attr("ui-type")){
            var uiType = theElement.attr("ui-type");
            switch (uiType) {
                case 'file': // 
                    error.insertAfter(theElement);
                    break;
                case 'date': // 
                    error.insertAfter(theElement);
                    break;
                case 'img': // 
                    error.insertAfter(theElement);
                    break;
                case 'opinionBox':
                	break;
                default:
                    error.insertAfter(theElement.parent());
            }
        }else{
            if (theElement.is("select")) {
                error.insertAfter(theElement);
            } else if (theElement.prop("type") === "checkbox") {
            	if(theElement.parents(".wui-valid-tip") && theElement.parents(".wui-valid-tip").length > 0){
                	var theParents = theElement.parents(".wui-valid-tip");
                	var place = theParents.attr("wui-valid-tip");
                	theParents[place](error);
                }else{
                	error.insertAfter(theElement);
                }
            } else if (theElement.prop("type") === "radio") {
                if(theElement.parents(".wui-valid-tip") && theElement.parents(".wui-valid-tip").length > 0){
                	var theParents = theElement.parents(".wui-valid-tip");
                	var place = theParents.attr("wui-valid-tip");
                	theParents[place](error);
                }else{
                	error.insertAfter(theElement);
                }
            } else {
                error.insertAfter(theElement);
            }
        }
	}
    
