;(function (ui, $, undefined){
    /*
     * 
     * @Author   Chenzx
     * @DateTime 2018-11-28
     * @param    {JSON}     obj        
     * @param    {JSON}     opt        
     * @param    {Object}   component  
     * @param    {Boolean}  parentNode 
     */
    function SelectNode(obj, opt, component, parentNode){
        
        // 
        this.opt = $.extend(true, {}, this.getInitOpt(), opt);

        // 
        this.component = component;

        // key
        this.key = obj[this.opt["name"]];
        
        this.textFields = ui.getTextField(this.opt["text"]);

        // 
        this.title = obj[this.opt["text"]];
        // delete obj[opt["text"]];

        // 
        this.enable = !!obj[this.opt.jsonReader["disabled"]] ? false : true;
        delete obj[this.opt.jsonReader["disabled"]];

        // 
        this.group = obj[this.opt.jsonReader["children"]] ? true : false;

        // 
        this.selected = obj[this.opt.jsonReader["selected"]] ? true : false;
        delete obj[this.opt.jsonReader["selected"]];

        // 
        this.hidden = obj[this.opt.jsonReader["hidden"]] ? true : false;
        delete obj[this.opt.jsonReader["hidden"]];

        // 
        this.data = obj;
        if(typeof(obj) === "object"){
            this.item = obj;
        }

        if(typeof(obj) === "string"){
            this.item = {};
            this.item[opt["name"]] = obj;
            for(var _i = 0; _i < this.textFields.length; _i++){
                var jTextField = this.textFields[_i];
                this.item[jTextField.value] = obj;
            }
        }

        // 
        this.renderTitle(this.item);

        // 
        this.childrens = [];

        // 
        this.parent = parentNode;

        // 
        this.optionTmp = '<li tabindex=0 data-original-index="' + this.component.pickerNodes.length + '">'
                    + '<a>'
                        + '<span class="text"><i class="' + this.opt.classList.option + '"></i> ' + this.title + '</span>'
                        + '<span class="' + this.opt.classList.checked + ' check-mark">'
                        + '</span>'
                    + '</a>'
                + '</li>';

        // 
        this.optgroupTmp = '<li class="dropdown-header" tabindex=0 data-optgroup-index="' + this.component.pickerNodes.length + '">'
                            + '<span class="text"><i class="' + this.opt.classList.group + '"></i> ' + this.title + '</span>'
                            + '<span class="' + this.opt.classList.checked + ' check-mark">'
                        + '</li>';

        // 
        this.dividerTmp = '<li class="divider" data-divider-index="{0}"></li>',

        // 
        this.node = null;

        this.renderNode();
    }

    SelectNode.prototype = {
        constructor: SelectNode,

        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-12-26
         * @return   {[type]}   [description]
         */
        getInitOpt: function(){
            return {
                // {string}  ,text
                text: "name"          
                // {string}  ,value
                , name: "id" 
                // 
                , jsonReader: {
                    // {String} 
                    children: "children"
                    // {String} 
                    , disabled: "disabled"
                    // {String} 
                    , selected: "selected"
                    // {String} 
                    , hidden: "hidden"
                }
                // 
                , classList: {
                    // 
                    "selected": "selected"
                    // 
                    , "hidden": "hide"
                    // 
                    , "disabled": "disabled"
                    // 
                    , "active": "active"
                    // {string}  
                    , "checked": "fa fa-check"
                    // {String} ,
                    , "group": "fa fa-group  wui-fa-middle"
                    // {String} 
                    , "option": ""
                }
                // 
                , rules: {
                    // {function} ,boolean
                    hidden: null
                    // {function} ,boolean
                    , disabled: null
                    // {function} ,boolean
                    , selected: null
                }
                // ,,false
                , isSelectGroup: false       
                // {number} ,false
                , maxNumber: false
            };
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-10-26
         * @return   {[type]}   [description]
         */
        renderNode: function(){
            var that = this, obj = this.data;

            if(this.group){
                this.node = $(this.optgroupTmp);
                this.$divider1 = $(this.dividerTmp);

                this.component.addPickerItem(this.$divider1);
                this.component.addPickerItem(this.node);
            }else{
                this.node = $(this.optionTmp);

                if(this.parent){
                    this.node.children('a').addClass('opt');
                }
                this.component.addPickerItem(this.node);
            }

            this.node.children('a').attr("title", this.title);

            this.node.on("click", "a, span", function(ev){
                ev.stopPropagation();
                // console.info(this);
                that.click();
            })

            // 
            this.node.focus(function(){
                that.addClass(that.opt.classList.active);
            }).blur(function(){
                that.removeClass(that.opt.classList.active);
            }).hover(function(){
                that.focus();
            }, function(){
                that.blur();
            })

            //Enter
            this.node.keydown(function(event){
                // event.stopPropagation();
                // event.preventDefault();
                // alert("down")
            }).keyup(function(event){
                event.stopPropagation();
                // event.preventDefault();
                
                var keynum = window.event ? event.keyCode : event.which;
                // alert(keynum)
                if(keynum == ui.CONSTANTS.KEYS.ENTER || keynum == ui.CONSTANTS.KEYS.OTHER){
                    that.enter(this);
                }else if(keynum == ui.CONSTANTS.KEYS.DOWN){
                    var nextSiblingNode = that.getNextSibling();

                    for(var i = 0; i < nextSiblingNode.length; i++){
                        var node = nextSiblingNode[i];
                        if(!node.isHidden() && node.isEnable()){
                            that.blur();
                            node.focus();
                            break;
                        }
                    }
                }else if(keynum == ui.CONSTANTS.KEYS.UP){
                    var prevSiblingNode = that.getPrevSibling();
                    for(var i = (prevSiblingNode.length - 1); i >= 0; i--){
                        var node = prevSiblingNode[i];
                        if(!node.isHidden() && node.isEnable()){
                            that.blur();
                            node.focus();
                            break;
                        }
                    }
                }
            })

            this.nodeRenderStatus(this.opt.rules);
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-11-29
         * @return   {[type]}   [description]
         */
        renderTitle: function(itemData){
            var that = this,
                text = this.opt.text,
                // 
                showTitle = text;

            if(this.textFields.length > 0){
                for(var _i = 0; _i < this.textFields.length; _i++){
                    var jTextField = this.textFields[_i];
                    if(ui.isUndefined(itemData[jTextField.value])){
                        itemData[jTextField.value] = "";
                    }

                    showTitle = showTitle.replace(jTextField.match, itemData[jTextField.value]);
                }
            }else{
                // text,
                if(typeof(itemData) === "string"){
                    showTitle = itemData;
                }else{
                    showTitle = JSON.stringify(itemData);
                }
            } 

            // 
            this.title = showTitle;

            // this.node.find('[wui-attr="title"]').each(function(i){
            //     $(this).html(that.title);
            // })

            return that.title;
        },
        click: function(){
            this.setSelected(!this.isSelected());
        },
        enter: function(){
            this.setSelected(!this.isSelected());
        },
        focus: function(){
            this.node.focus();
        },
        blur: function(){
            this.node.blur();
        },
        hover: function(){
            this.node.hover();
        },
        getPrevSibling: function(){
            var index = this.component.pickerNodes.indexOf(this);
            var prevSiblingNode = this.component.pickerNodes.slice(0, index);
            return prevSiblingNode;
        },
        getNextSibling: function(){
            var index = this.component.pickerNodes.indexOf(this);
            var nextSiblingNode = this.component.pickerNodes.slice(index + 1, this.component.pickerNodes.length);
            return nextSiblingNode;
        },
        setNodeSelected: function(isSelected, isSelectGroup, isExcute){
            var enable = true;

            if(isSelected == this.isSelected() || !this.isEnable()){
                return;
            }

            // ,
            if(isSelected && this.isHidden()){
                return;
            }

            isSelectGroup = ui.isUndefined(isSelectGroup) ? this.opt.isSelectGroup : isSelectGroup;
            isExcute = ui.isUndefined(isExcute) ? true : isExcute;

            if(this.isGroup() && !isSelectGroup){
                return;
            }

            if(isExcute){
                var isContinue = true
                if(isSelected){
                    isContinue = this.component.trigger("beforeItemSelect", {
                        srcElement: this.node.get(0),
                        item: this.data,
                        node: this
                    });
                }else{
                    isContinue = this.component.trigger("beforeItemUnselect", {
                        srcElement: this.node.get(0),
                        item: this.data,
                        node: this
                    });
                }
                
                if(isContinue === false){
                    return false;
                }
            }

            if(this.isGroup()){
                if(isSelected && typeof(this.opt.maxNumber) === "number"
                        && this.getEnableChildrens(false).length > (this.opt.maxNumber - this.component.getComponentNodes().length)){
                    enable = false;
                }
                
                if(this.isEnable() && enable){

                    if(!this.component.isFilter){
                        this.selected = isSelected ? true : false;
                        if(this.selected){
                            this.addClass('selected');
                        }else{
                            this.removeClass('selected');
                        }
                    }

                    $.each(this.childrens, function(i, childNode){
                        childNode.setNodeSelected(isSelected, isSelectGroup, isExcute);
                    })
                } 
            }else{
                this.selected = isSelected ? true : false;
                if(this.selected){
                    this.addClass(this.opt.classList.selected);

                    this.component.addComponentNodes(this, isExcute);
                }else{
                    this.removeClass(this.opt.classList.selected);

                    this.component.removeComponentNodes(this, isExcute);
                }

                if(!this.selected && this.parent){
                    this.parent.setNodeSelected(false, false);
                }
            }

            // ,
            if(this.opt.maxNumber == 1){
                this.component.closePicker();
            }
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-10-30
         * @param    {Boolean}  isSelected [description]
         * @return   {[type]}              [description]
         */
        getEnableChildrens: function(isSelected){
            var that = this, resultNode = [];
            $.each(this.childrens, function(i, childNode){
                if(childNode.isEnable() && !childNode.isHidden()){
                    if(ui.isUndefined(isSelected)){
                        resultNode.push(childNode);
                    }else if(childNode.isSelected() === isSelected){
                        resultNode.push(childNode);
                    }
                }
            })
            return resultNode;
        },
        addClass: function(className){
            this.node.addClass(className);
        },
        removeClass: function(className){
            this.node.removeClass(className);
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-10-28
         * @param    {Boolean}  isSelected [description]
         */
        setSelected: function(isSelected){

            // ,,
            if(this.opt.maxNumber === 1 && this.component.getComponentNodes().length > 0 && isSelected){

                while(this.component.getComponentNodes().length > 0){
                    var lastSelectedNode = this.component.popNodes();
                    lastSelectedNode.setNodeSelected(false, undefined, false);
                }   
            }

            if(isSelected){
                var isMaxNumber = this.component.isMaxNumber(this.data);
                if(isMaxNumber){
                    return false;
                }
            }

            this.setNodeSelected(isSelected);

            this.component.renderResult();
        },
        isSelected: function(){
            return this.selected;
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-10-28
         * @param    {Boolean}  isEnable [description]
         */
        setEnable: function(isEnable){
            this.enable = isEnable ? true : false;
            if(this.enable){
                this.removeClass(this.opt.classList.disabled);
            }else{
                this.addClass(this.opt.classList.disabled);
            }
        },
        isEnable: function(){
            return this.enable;
        },
        setNodeHidden: function(isHidden, isHiddenGroup){
            this.hidden = isHidden ? true : false;
            // ui.notice(this.hidden)
            if(this.hidden){
                this.addClass(this.opt.classList.hidden);
            }else{
                this.removeClass(this.opt.classList.hidden);
            }
            if(this.isGroup() && isHiddenGroup){
                $.each(this.childrens, function(i, childNode){
                    childNode.setNodeHidden(isHidden, isHiddenGroup);
                })
            }
        },
        setHidden: function(isHidden){
            this.setNodeHidden(isHidden);
        },
        isHidden: function(){
            return this.hidden;
        },
        isGroup: function(){
            return this.group;
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-10-28
         * @return   {[type]}   [description]
         */
        nodeRenderStatus: function(rules){

            //  this.isEnable() 
            if(this.node.hasClass(this.opt.classList.disabled) === this.isEnable()){
                this.setEnable(this.isEnable());
            }

            //  this.isHidden() 
            if(this.node.hasClass(this.opt.classList.hidden) !== this.isHidden()){
                this.setNodeHidden(this.isHidden());
            }

            //  this.isSelected() 
            if(this.node.hasClass(this.opt.classList.selected) !== this.isSelected()){
                this.selected = !this.isSelected();
                this.setSelected(!this.selected);
            }

            this.component.trigger("nodeRender", this);
        }
    }

    // 
    var _global = (function(){ return this || (0, eval)('this'); }());
    if (typeof module !== "undefined" && module.exports) {
        module.exports = SelectNode;
    } else if (typeof define === "function" && define.amd) {
        define(function(){return SelectNode;});
    } else {
        !('SelectNode' in _global) && (_global.SelectNode = SelectNode);
    }

})( wui || {}, libs );