/**
 * <h3>WUI </h3>
 * <h5></h5>
 * <ol>
 * <li>{{#crossLink "wui.fillForm"}}{{/crossLink}}:  </li>
 * <li>{{#crossLink "wui.getForm"}}{{/crossLink}}:  </li>
 * <li>{{#crossLink "wui.emptyForm"}}{{/crossLink}}:  </li>
 * </ol>
 * <h5></h5>
 * <ol>
 * <li>{{#crossLink "wui.readonly"}}{{/crossLink}}:  </li>
 * <li>{{#crossLink "wui.readwrite"}}{{/crossLink}}:  </li>
 * <li>{{#crossLink "wui.disabled"}}{{/crossLink}}:  </li>
 * <li>{{#crossLink "wui.enabled"}}{{/crossLink}}:  </li>
 * </ol>
 * @module Form
 */

;(function (ui,$, undefined) {
    /**
     * <p>(),namekeyname,idkey(id)</p>
     * @namespace wui
     * @class getForm
     * @constructor 
     * @param [form] {string} DOM,"[role='form']""body"
     * @param [ignore] {string} ,"#id,.class",""
     * @param [dataType] {string} ,:"value"/"text"/"json","value"
     * @param [ueditorDataType] {string} wui.ueditor,:"html"/"text"/"plain"/"all","html"
     * @param [isString] {Boolean} value",",["id1","id2"]"id1,id2",false
     * @return {json} [:{"key": "value",...}]
     * @example
     *  
     *   js: 
     *   
            // 
            wui.getForm("[role='form']");

            // data-ignore
            wui.getForm("[role='form']", '[data-ignore]');
     *
     */
    ui.getForm = function(form, ignore, dataType, ueditorType, isString) {
        ui.logUtilConfig.debugLog.methodName = "getForm";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("getForm", FRAME_NAME);
        // ui.logParamValue("form",form);

        var $select = $("body");
        if (form) {
            
            if(ui.isJQuery(form)){
                $select = form;
            }else{
                $select = $(form);
            }
        }

        if(ignore === true){
            isString = true;

            ignore = "";  // :hidden:
            dataType = dataType || "value";
            ueditorType = ueditorType || "html";
        }else if(dataType === true){
            isString = true;

            ignore = ignore || "";  // :hidden:
            dataType = "value";
            ueditorType = ueditorType || "html";
        }else if(ueditorType === true){
            isString = true;
            
            ignore = ignore || "";  // :hidden:
            dataType = dataType || "value";
            ueditorType = "html";
        }else{
            ignore = ignore || "";  // :hidden:
            dataType = dataType || "value";
            ueditorType = ueditorType || "html";
        }

        // isString = isString || false;
        var invalidControls = "[type='button'],[type='submit'],[type='reset'],[wui-widget-hidden]";   // 

        var data = {};
        $select.find("input:not(" + invalidControls + "),select,textarea,[ui-type]:not(" + invalidControls + ")").not(ignore).each(function(index, elem){
            var key = $(elem).attr("name") || $(elem).attr("id");
            if(!key){
                return true;
            }
            if($(elem).attr("ui-type")){
                var id = $(elem).attr("id");
                var widgetName = $(elem).attr("ui-type");
                if(!id){
                    ui.logWarn(widgetName + "id", FRAME_NAME+".getForm", true);
                    return true;
                }

                if(widgetName === "file" || widgetName === "img"){
                    id = $(elem).parent().attr("_fileId")
                }
                // console.info(widgetName);
                switch(widgetName){
                    case "ueditor":
                        var oWidget = wui[widgetName](id);
                        data[key] = oWidget.get(ueditorType);
                        break;
                    case "":
                        ui.logWarn(widgetName + "", FRAME_NAME+".getForm", true);
                        break;
                    // case "date":
                    // case "selectBox":
                    // case "tagsinput":
                    // case "chooseBox":
                    // case "selectComboBox":
                    // case "file":
                    // case "img":
                    // case "tagBox":
                    // case "tagComboBox":
                    //     var oWidget = wui[widgetName](id);
                    //     data[key] = oWidget.get("value");
                    //     break;
                    default:
                        var oWidget = wui[widgetName](id);
                        var value = oWidget.get(dataType);
                        // console.error(value)
                        
                        if(value !== null){
                            if(isString && (dataType == "value" || dataType == "text")){
                                data[key] = ui.isArray(value) ? value.join(",") : value;
                            }else{
                                data[key] = value;
                            }
                        }
                        break;
                }
            }else{
                var tagName = $(elem)[0].tagName;
                var type = $(elem).attr('type');
                // var theVal = (data[key] === undefined || data[key] == "") ? [] : data[key].split(",");
                var theVal = [];
                if(!ui.isEmptyType(data[key])){
                    theVal = data[key].split(",");
                }
                
                // console.info(theVal);
                if (tagName == 'INPUT') {
                    if (type == 'radio') {
                        // theVal.push($('[name="' + key + '"]:checked').val());
                        if($(elem).prop("checked") && $(elem).val() != ""){
                            theVal.push($(elem).val());
                        }
                    } else if (type == 'checkbox') {
                        // alert($(elem).val() + " : " + $(elem).prop("checked"));
                        if($(elem).prop("checked") && $(elem).val() != ""){
                            theVal.push($(elem).val());
                        }
                    } else {
                        if($(elem).val() != ""){
                            theVal.push($(elem).val());
                        }
                    }
                }else if (tagName == 'SELECT' || tagName == 'TEXTAREA') {
                    if($(elem).val() != ""){
                        theVal.push($(elem).val());
                    }
                }
                data[key] = theVal.join(",");
            }
        })
        // console.info(data);
        return data;
    }

    /**
     * <p>()</p>
     * @namespace wui
     * @class fillForm
     * @constructor 
     * @param data {json} ,{1 Name:1 Value, 2 Name:2 Value, ...}
     * @param onBeforeFill {Function} ,element:,data:,false
     * @param onFilled {Function} ,element:,isFillSuccess: (false,true),data:
     * @param [form] {string} DOM,"[role='form']""body"
     * @example
     *
     *   html:
     *
        <input type="text" id="name" />
        <input type="text" id="email" />
        <p id="remark"></p>
     *  
     *   js: 
     *
            var data = {
                name: "",
                email: "123456789@qq.com",
                remark: ""
            }
            // 1:
            wui.fillForm( data );   // input

            // 2:,
            wui.fillForm(data, null, function(element, isFillSuccess, value){
                if(!isFillSuccess){
                    $(element).text(value);
                }
            });

            // 3:(email)
            wui.fillForm(data, function(element, value){
                if(element.id === "name"){
                    return false;
                }
            });
     *
     */
    ui.fillForm = function(data, onBeforeFill, onFilled, form) {
        ui.logUtilConfig.debugLog.methodName = "fillForm";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("fillForm", FRAME_NAME);

        var $select = $("body");
        if (form) {
            
            if(ui.isJQuery(form)){
                $select = form;
            }else{
                $select = $(form);
            }
        }

        var obj = data || {};
        var key, value, tagName, type, arr;
        for (x in obj) {
            key = x;
            value = obj[x];
            arr = [];
            // if(!value && value!= 0){
            //     continue;
            // }
            $select.find("[name='" + key + "'],[name='" + key + "[]'], [id='" + key + "'],[id='" + key + "[]']").each(function(index, elem) {
                var isFillSuccess = false, ret = true;
                if(onBeforeFill && typeof(onBeforeFill) === "function"){
                    ret = onBeforeFill.call(this, this, value);
                }
                if(ret === false){
                    return true;
                }

                if($(this).attr("ui-type")){
                    
                    var widgetName = $(elem).attr("ui-type");
                    var widgetId = $(elem).attr("id") ? $(elem).attr("id") : $(elem).attr("name");
                    if(!(ui[widgetName])){
                        ui.logWarn(widgetName + "", "wui.fillForm", true);
                        return true;
                    }
                    var theWidget = ui[widgetName](widgetId);
                    if (!theWidget) {
                        ui.logWarn(widgetName + "", "wui.fillForm", true);
                        return true;
                    }
                    if(!ui.isUndefined(value)){
                        if (theWidget.set) {
                            theWidget.set(value);
                            isFillSuccess = true;
                        }else if(theWidget.select){
                            theWidget.select(value);
                            isFillSuccess = true;
                        }else{
                            ui.logWarn(widgetName + " set()/select() ", "wui.fillForm", true);
                            return true;
                        }
                    }else{
                        if (theWidget.empty) {
                            theWidget.empty();
                            isFillSuccess = true;
                        }
                    }
                        
                }else{
                    tagName = $(this)[0].tagName;
                    type = $(this).attr('type');
                    if(typeof(value) === "boolean"){
                        value = value.toString();
                    }
                    if (tagName == 'INPUT') {
                        if (type == 'radio') {
                            $(this).prop('checked', $(this).val() == value);
                            isFillSuccess = true;
                        } else if (type == 'checkbox') {
                            if(typeof(value) === "string"){
                                arr = value.split(',');
                            }else if(typeof(value) === "number"){
                                // arr.push(value.toString());
                                arr.push(value);
                            }else if(ui.isJson(value)){
                                arr.push(value);
                            }
                            
                            for (var i = 0; i < arr.length; i++) {
                                if ($(this).val() == arr[i]) {
                                    $(this).prop('checked', true);
                                    // break;
                                }else{
                                    $(this).prop('checked', false);
                                }
                            }
                            isFillSuccess = true;
                        } else {
                            $(this).val(value);
                            isFillSuccess = true;
                        }
                    } else if (tagName == 'SELECT') {
                        // value = value.toString();
                        $(this).val(value);
                        isFillSuccess = true;
                    } else if (tagName == 'TEXTAREA') {
                        // value = value.toString();
                        $(this).val(value);
                        if(!ui.isUndefined($(this).attr("data-auto-height"))){
                            $(this).change();
                        }
                        isFillSuccess = true;
                    }
                }
                    
                if(onFilled && typeof(onFilled) === "function"){
                    onFilled.call(this, this, isFillSuccess, value);
                }
                // ui.logMessage(key + ' = ' + $(this).val());
            });
        }
        return true;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class emptyForm
     * @constructor  
     * @param [form] {string} DOM,"[role='form']""body"
     * @param [ignore] {string} ,"#id,.class",""
     * @example
     *  
     *   js: 
     *   
            // 
            wui.emptyForm();
     *
     */
    ui.emptyForm = function(form, ignore, isValidate) {
        ui.logUtilConfig.debugLog.methodName = "emptyForm";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("emptyForm", FRAME_NAME);

        var $select = $("body");
        if (form) {
            if(ui.isJQuery(form)){
                $select = form;
            }else{
                $select = $(form);
            }
        }


        ignore = ignore || "";
        if(ui.isUndefined(isValidate)){
            isValidate = true;
        }
        var invalidControls = "[type='button'],[type='submit'],[type='reset'],[wui-widget-hidden]";   // 

        var data = {}, tagName, type;
        $select.find("input:not(" + invalidControls + "),select,textarea,[ui-type]").not(ignore).each(function(index, elem){
            var key = $(elem).attr("name") || $(elem).attr("id");
            if($(elem).attr("ui-type")){
                var id = $(elem).attr("id");
                var widgetName = $(elem).attr("ui-type");
                if(!id){
                    ui.logWarn(widgetName + "id", FRAME_NAME+".getForm", true);
                    return true;
                }

                if(widgetName === "file" || widgetName === "img"){
                    id = $(elem).parent().attr("_fileId")
                }
                // console.info(widgetName);
                var oWidget = wui[widgetName](id);
                switch(widgetName){
                    case "":
                        ui.logWarn(widgetName + "", FRAME_NAME+".emptyForm", true);
                        break;
                    default:
                        data[key] = oWidget.empty(isValidate);
                        break;
                    // case "date":
                    // case "selectBox":
                    // case "tagsinput":
                    // case "chooseBox":
                    // case "selectComboBox":
                    // case "file":
                    // case "img":
                    // case "tagBox":
                    // case "tagComboBox":
                    // case "ueditor":
                    //     data[key] = oWidget.empty(isValidate);
                    //     break;
                    // default:
                    //     ui.logWarn(widgetName + " ", FRAME_NAME+".emptyForm", true);
                    //     break;
                }
            }else{
                tagName = $(elem)[0].tagName;
                type = $(elem).attr('type');
                if (tagName == 'INPUT') {
                    if (type == 'radio') {
                        $(elem).prop('checked', false);
                    } else if (type == 'checkbox') {
                        $(elem).prop('checked', false);
                    } else {
                        $(elem).val("");
                    }
                } else if (tagName == 'SELECT') {
                    $(elem).val("");
                } else if(tagName == 'TEXTAREA'){
                    $(elem).val("");
                }
            }  
        })
        return true;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class readonly
     * @constructor 
     * @param [form] {string} DOM,"[role='form']""body"
     * @param [ignore] {string} ,"#id,.class",""
     * @param [callback] {function} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.readonly();
     *
     */
    ui.readonly = function (form, ignore, callback){
        ui.logUtilConfig.debugLog.methodName = "readonly";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("readonly", FRAME_NAME);
        
        var select = $("body");
        if (!!form) {
            select = $(form);
        }
        ignore = ignore || "";
        var commonLabel = ["input", "select", "textarea"];
        // 
        select.find("*").not(ignore).each(function(index, elem){
            var tagName = $(elem)[0].tagName.toLowerCase();
            // 
            if($(elem).attr("ui-type")){
                var widgetName = $(elem).attr("ui-type");
                var widgetId = $(elem).attr("id");

                if(widgetName === "file" || widgetName === "img"){
                    widgetId = $(elem).parent().attr("_fileId")
                }

                var theWidget = ui[widgetName](widgetId);
                if (!theWidget) {
                    ui.logWarn(widgetName + "", "wui.readonly", true);
                    return true;
                }
                if (!theWidget.readonly) {
                    ui.logWarn(widgetName + "", "wui.readonly", true);
                    return true;
                }
                theWidget.readonly();
            }else if(commonLabel.indexOf(tagName) > -1){
                if(tagName === "select"){
                    $(elem).attr('disabled', true).addClass("wui-readonly");
                }else{
                    var type = $(this).attr('type');
                    if (type == 'radio') {
                        $(elem).attr('disabled', true).addClass("wui-readonly");
                    } else if(type == 'checkbox'){
                        $(elem).attr('disabled', true).addClass("wui-readonly");
                    }else{
                        $(elem).attr('readonly', true);
                    }
                    
                }
                
                // $(elem).addClass("cursor-default");
            }
        })
        // $(select).find("*").attr('disabled', true);

        callback && typeof(callback)==="function" && callback(form);

        return true;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class readwrite
     * @constructor 
     * @param [form] {string} DOM,"[role='form']""body"
     * @param [ignore] {string} ,"#id,.class",""
     * @param [callback] {function} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.readwrite();
     *
     */
    ui.readwrite = function (form, ignore, callback){
        ui.logUtilConfig.debugLog.methodName = "readwrite";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("readwrite", FRAME_NAME);

        var select = $("body");
        if (!!form) {
            select = $(form);
        }

        ignore = ignore || "";
        // 
        var commonLabel = ["input", "select", "textarea"];
        select.find("*").not(ignore).each(function(index, elem){
            var tagName = $(elem)[0].tagName.toLowerCase();
            // 
            if($(elem).attr("ui-type")){
                var widgetName = $(elem).attr("ui-type");
                var widgetId = $(elem).attr("id");
                
                if(widgetName === "file" || widgetName === "img"){
                    widgetId = $(elem).parent().attr("_fileId")
                }
                
                var theWidget = ui[widgetName](widgetId);
                if (!theWidget) {
                    ui.logWarn(widgetName + "", "wui.readwrite", true);
                    return true;
                }
                if (!theWidget.readwrite) {
                    ui.logWarn(widgetName + "", "wui.readwrite", true);
                    return true;
                }
                theWidget.readwrite();
            }else if(commonLabel.indexOf(tagName) > -1){
                if(tagName === "select"){
                    $(elem).removeAttr('disabled').removeClass("wui-readonly");
                }else{
                    var type = $(this).attr('type');
                    if (type == 'radio') {
                        $(elem).removeAttr('disabled').removeClass("wui-readonly");
                    } else if(type == 'checkbox'){
                        $(elem).removeAttr('disabled').removeClass("wui-readonly");
                    }else{
                        $(elem).removeAttr('readonly');
                    }
                }
                
                // $(elem).removeClass("cursor-default");
            }
        })

        callback && typeof(callback)==="function" && callback(form);

        return true;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class disabled
     * @constructor 
     * @param [form] {string} DOM,"[role='form']""body"
     * @param [ignore] {string} ,"#id,.class",""
     * @param [callback] {function} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.disabled();
     *
     */
    ui.disabled = function (form, ignore, callback){
        ui.logUtilConfig.debugLog.methodName = "disabled";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("disabled", FRAME_NAME);

        var select = $("body");
        if (!!form) {
            select = $(form);
        }
        ignore = ignore || "";
        var commonLabel = ["input", "select", "button", "a", "textarea"];
        // 
        select.find("*").not(ignore).each(function(index, elem){
            var tagName = $(elem)[0].tagName.toLowerCase();
            // 
            if($(elem).attr("ui-type")){
                var widgetName = $(elem).attr("ui-type");
                var widgetId = $(elem).attr("id");

                if(widgetName === "file" || widgetName === "img"){
                    widgetId = $(elem).parent().attr("_fileId")
                }

                var theWidget = ui[widgetName](widgetId);
                if (!theWidget) {
                    ui.logWarn(widgetName + "", "wui.disabled", true);
                    return true;
                }
                if (!theWidget.disabled) {
                    ui.logWarn(widgetName + "", "wui.disabled", true);
                    return true;
                }
                theWidget.disabled();
            }else if(commonLabel.indexOf(tagName) > -1){
                $(elem).attr('disabled', true);
                $(elem).addClass("cursor-default");
            }
        })
        // $(select).find("*").attr('disabled', true);

        callback && typeof(callback)==="function" && callback(form);

        return true;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class enabled
     * @constructor 
     * @param [form] {string} DOM,"[role='form']""body"
     * @param [ignore] {string} ,"#id,.class",""
     * @param [callback] {function} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.enabled();
     *
     */
    ui.enabled = function (form, ignore, callback){
        ui.logUtilConfig.debugLog.methodName = "enabled";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("enabled", FRAME_NAME);

        var select = $("body");
        if (!!form) {
            select = $(form);
        }

        ignore = ignore || "";
        // 
        var commonLabel = ["input", "select", "button", "a", "textarea"];
        select.find("*").not(ignore).each(function(index, elem){
            var tagName = $(elem)[0].tagName.toLowerCase();
            // 
            if($(elem).attr("ui-type")){
                var widgetName = $(elem).attr("ui-type");
                var widgetId = $(elem).attr("id");
                
                if(widgetName === "file" || widgetName === "img"){
                    widgetId = $(elem).parent().attr("_fileId")
                }
                
                var theWidget = ui[widgetName](widgetId);
                if (!theWidget) {
                    ui.logWarn(widgetName + "", "wui.enabled", true);
                    return true;
                }
                if (!theWidget.enabled) {
                    ui.logWarn(widgetName + "", "wui.enabled", true);
                    return true;
                }
                theWidget.enabled();
            }else if(commonLabel.indexOf(tagName) > -1){
                $(elem).removeAttr('disabled');
                $(elem).removeClass("cursor-default");
            }
        })
        // $(select).find("*").removeAttr('disabled');

        callback && typeof(callback)==="function" && callback(form);

        return true;
    }

    return ui;
})(wui || {}, libs);