                    , otherParam: {}
                    // {boolean} 
                    , isClickTriggerPopup: true
                    // {boolean} 
                    , isVoluation: false        
                    // {Boolean} ,false,
                    , isShowInputBar: true
                    // {Boolean} 
                    , isOpenWithNodes: false
                    // {Boolean} 
                    , isDisableRemove: false

                    /*  */
                    // {string} 
                    , pageHeight: ui.pageHeight
                    // {string}  
                    , pageWidth: ui.pageWidth
                    // {Boolean} ,false,pageHeightpageWidth
                    , isFull: false             
                    // {string} 
                    , pageTitle: ': ' + id+' '     

                    /*  */
                    // {string} 
                    , treeTitle: '' 
                    // {string} URL             
                    , treeUrl: ''
                    // ,key,"parentDeptId" 
                    , lazyField: 'parentDeptId'
