                    // itemsText: [],

                    // 
                    opt: params,

                    // 
                    // 
                    // viewConTmp: '<div class="tag-container wui-input" wui-attr="view"></div>',
                    viewConTmp: '<div class="tag-container btn-group wui-select-box dropdown"><div wui-attr="view" class="wui-view-tags"></div></div>',

                    // 
                    inputBoxTmp: '<input type="text" wui-widget-hidden="true" wui-attr-clear-node="true" wui-attr="input"/>',

                    // 
                    tipTmp: '<span class="placeholder">{0}</span>',

                    // 
                    pickerTmp: '<ul wui-attr="menu" class="dropdown-menu focus inner" role="menu" style="overflow-y: auto;"></ul>',
                    
                    // 
                    // ([wui-attr="container"]): $element + $viewBox
                    $container: null,
                    // ([wui-attr="view"]):$tags + $input
                    $viewBox: null,
                    // 
                    $viewCon: null,
                    // (input)
                    $input: null,
                    // (.dropdown-menu.wui-open)
                    $dropdownBox: null,
                    // ([wui-attr="menu"].dropdown-menu)
                    $selectContentBox: null, 
                    // 
                    pickerNodes: [],

                    // 
                    data: [],

                    // 
                    // errorElement: null,
                    
                    // 
                    // _tagsComponent: null,

