// WUI 
;(function (ui,$, undefined) {

    /**
     * <p></p>
     * @namespace wui
     * @class openWindow
     * @constructor
     * @param setting {JSON} 
     * @param setting.pageUrl {String} 
     * @param setting.pageParams {JSON} 
     * @param setting.pageName {JSON} 
     * @param setting.pageSetting {JSON} (),'height=100,width=400,top=0,left=0,toolbar=no,menubar=no,scrollbars=no, resizable=no,location=no, status=no'
     * @example
     *  
     *   js: 
     *   
            // ,
            wui.openWindow({
                pageUrl: "http://xxx/xx.html?id=0102",
                pageParams: {},
                pageName: "",
                pageSetting: ""
            });
     *
     */
    ui.openWindow = function(setting){
        var config = {
            pageUrl: "",
            pageParams: {},
            pageName: "",
            pageSetting: ""
        }

        setting = $.extend(true, {}, config, setting);
        var pageUrl = ui.buildUrl(setting.pageUrl, setting.pageParams);

        window.WUI_WINDOW_FLAG = ui.guid();

        window.open(pageUrl, setting.pageName, setting.pageSetting);
    }

    /**
     * <p>,(,isRefreshfalse)</p>
     * @namespace wui
     * @class closeWindow
     * @constructor
     * @param setting {JSON} 
     * @param setting.onBeforeClose {Function} ,theParentWindow:,iframe,iframe
     * @param setting.isRefresh {Boolean} ,true(iframeiframe,,onBeforeClosetheParentWindow.parent,)
     * @example
     *  
     *   js: 
     *   
            // ,,
            wui.closeWindow({
                onBeforeClose: function(theParentWindow){

                    theParentWindow.parent.alertMessage("123");
                }
            })
     *
     */
    ui.closeWindow = function(setting){
        var config = {
            onBeforeClose: ui.CONSTANTS.EMPTYFUNC,
            isRefresh: true
        }

        setting = $.extend(true, {}, config, setting);

        try{
            if(setting.onBeforeClose && typeof(setting.onBeforeClose) === "function"){
                setting.onBeforeClose(window.opener);
            }
            if(setting.isRefresh){
                var WUI_WINDOW_FLAG = window.WUI_WINDOW_FLAG;
                if(window.opener && !window.opener.closed){
                    if(window.opener.WUI_WINDOW_FLAG !== WUI_WINDOW_FLAG){
                        window.opener.location = window.opener.location;
                        // alert("")
                    }else{
                        // alert("");
                        window.opener.parent.location=window.opener.parent.location;
                    }
                }
            }
            window.open('', '_self');
            window.close();
        }catch(e){
            ui.logWarn(e, "wui.closeWindow", true);
            window.open('', '_self');
            window.close();
        }
    }



    /**
     * <p></p>
     * @namespace wui
     * @class backhome
     * @constructor
     * @param [page] {string} ,(wui.contextPath)
     * @example
     *  
     *   js: 
     *   
            // ,"http://xxx/xx.html?id=0102"
            wui.backhome("http://xxx/xx.html?id=0102");
     *
     */
    ui.backhome = function(page){
        ui.logUtilConfig.debugLog.methodName = "backhome";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("backhome", FRAME_NAME);

        if(page){
            window.top.location.href = page;
        }else{
            window.top.location.href = ui.contextPath;
        }
    }

    /*
     * <p>URL</p>
     * @namespace wui
     * @class encodeURI
     * @constructor 
     * @param uri {string} 
     * @return {string} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.encodeURI("http://xxx/xx.html?id=0102");
     *
     */
    ui.encodeURI = function(uri){
        // return encodeURI(uri);
        // encodeURI  ASCII~!@#$&*()=:/,;?+'
        // encodeURIComponent ASCII~!*()'
        // encodeURIComponentencodeURI
        return encodeURIComponent(uri);
    }

    
    /*
     * <p>URL</p>
     * @namespace wui
     * @class decodeURI
     * @constructor 
     * @param uri {string} 
     * @return {string} 
     * @example
     *  
     *   js: 
     *   
            // ,URL"http://xxx/xx.html?id=0102"
            var id = wui.decodeURI("010101");
     *
     */
    ui.decodeURI = function(uri){
        // return decodeURI(uri);
        return decodeURIComponent(uri);
    }

    /*
     * URL
     * @Author   Chenzx
     * @DateTime 2018-12-29
     * @param    {[type]}   value [description]
     * @return   {[type]}         [description]
     */
    ui.encodeURLParamValue = function(value, isCodeUrlValue){
        ui.logUtilConfig.debugLog.methodName = "encodeURLParamValue";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        var urlVal = value, extString = ui.CONSTANTS.URLVALUEEXTSTRING;

        if(isCodeUrlValue){
            switch(typeof(value)){
                case 'string':
                    urlVal = value;
                    break;
                case 'number':
                    urlVal = typeof(value) + extString + value;
                    break;
                case 'boolean':
                    urlVal = typeof(value) + extString + value;
                    break;
                case 'object':
                    urlVal = typeof(value) + extString + JSON.stringify(value);
                    break;
                case 'undefined':
                    urlVal = typeof(value) + extString + value;
                    break;
                default:
                    urlVal = typeof(value) + extString + value;

            }
        }

        return ui.encodeURI(urlVal);
    }

    /*
     * URL
     * @Author   Chenzx
     * @DateTime 2018-12-29
     * @param    {[type]}   value           [description]
     * @param    {[type]}   isCodeUrlValue  ,undefined,isCodeUrlValue > wui.CONSTANTS.ISCODEURLVALUE >
     * @return   {[type]}         [description]
     */
    ui.isDecodeUrlValue = function(value, isCodeUrlValue){
        var result = false, extString = ui.CONSTANTS.URLVALUEEXTSTRING;
        if(ui.isUndefined(isCodeUrlValue)){
            if(ui.isUndefined(ui.CONSTANTS.ISCODEURLVALUE)){
                var matchReg = new RegExp("^(number" + extString + "|object" + extString + "|string" + extString + "|boolean" + extString + "|undefined" + extString + ")","m"); // matchReg/^####.*?(?=####)/gim
                result = matchReg.test(value);
            }else{
                result = ui.CONSTANTS.ISCODEURLVALUE;
            }
        }else{
            result = isCodeUrlValue;
        }


        return result;
    }

    /*
     * URL
     * @Author   Chenzx
     * @DateTime 2018-12-29
     * @param    {[type]}   value [description]
     * @return   {[type]}         [description]
     */
    ui.decodeURLParamValue = function(value, isCodeUrlValue){
        ui.logUtilConfig.debugLog.methodName = "decodeURLParamValue";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        var urlVal = ui.decodeURI(value), extString = ui.CONSTANTS.URLVALUEEXTSTRING, type, realValue;
        // var matchReg =new RegExp("(?<=" + extString + ").*?(?=" + extString + ")","m"); // matchReg/^####.*?(?=####)/gim
        // "####strMatch####".match(matchReg);
        isCodeUrlValue = ui.isDecodeUrlValue(urlVal, isCodeUrlValue);

        if(isCodeUrlValue){
            var urlValue = urlVal.split(extString);
            if(urlValue && urlValue.length > 1){
                type = urlValue[0];
                realValue = urlValue[1];
                switch(type){
                    case 'string':
                        urlVal = realValue;
                        break;
                    case 'number':
                        urlVal = ui.number(realValue);
                        break;
                    case 'boolean':
                        urlVal = ui.boolean(realValue);
                        break;
                    case 'object':
                        urlVal = ui.parseToObj(realValue);
                        break;
                    case 'undefined':
                        urlVal = undefined;
                        break;
                    default:

                }
            }
        }

        return urlVal;
    }

    /**
     * <p>JSONurlURL</p>
     * @namespace wui
     * @class buildUrl
     * @constructor 
     * @param url {string} url
     * @param paramObject {json} 
     * @param [addRandomCode] {boolean} 
     * @param [isCodeUrlValue] {boolean} ,"boolean####value",false,wui.getQueryString()wui.getQueryStringByName()
     * @return {string} [ URL ]
     * @example
     *  
     *   js: 
     *   
            // 
            var url = wui.buildUrl("http://xxx/xx.html", {id: "0102"});   // "http://xxx/xx.html?id=0102"
     *
     */
    ui.buildUrl = function(url, paramObject, addRandomCode, isCodeUrlValue) {
        ui.logUtilConfig.debugLog.methodName = "buildUrl";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("buildUrl", FRAME_NAME);
        
        var subStrFlag = "&";    // 
        paramObject = paramObject || {};
        addRandomCode = addRandomCode || true;
        if(ui.isUndefined(isCodeUrlValue)){
            isCodeUrlValue = ui.CONSTANTS.ISCODEURLVALUE;
        }

        var params = {};
        params.url = url;
        var str = JSON.stringify(paramObject);
        if(str.length > 50){
            params.paramObject = "";
        }else{
            params.paramObject = paramObject;
        }
        params.addRandomCode = addRandomCode;
        
        ui.logParamValue({
            paramName: "params",
            paramValue: params, 
            methodName: "buildUrl", 
            className: FRAME_NAME, 
            isDebug: true
        });

        // wui.buildUrl("http://xxx.com?appId=123", {
        //     appId: "234",
        //     appName: ""
        // })

        if (addRandomCode) {
            paramObject.randomCode = new Date().valueOf();
        }
        var queryString = "";

        var urlQueryString = ui.getQueryString(url);
        if(url.indexOf("?") > -1){
            url = url.substring( 0, url.indexOf("?"));
        }
        // console.group();
        // console.error("urlQueryString:");
        // console.error(urlQueryString);
        // console.groupEnd();
        // console.group();
        // console.error("paramObject:");
        // console.error(paramObject);
        // console.groupEnd();

        paramObject = $.extend(true, {}, urlQueryString, paramObject);

        // console.group();
        // console.error("paramObject:");
        // console.error(paramObject)
        // console.groupEnd();
        // 
        for ( var attr in paramObject) {
            var value = paramObject[attr];
            if (queryString.length > 0) {
                queryString += subStrFlag;
            }

            queryString += attr + "=" + ui.encodeURLParamValue(value, isCodeUrlValue);
        }
            
        if (queryString.length > 0) {
            if (url.indexOf("?") >= 0) {
                url = url + subStrFlag + queryString;
            } else {
                url = url + "?" + queryString;
            }
        }
        
        
        if(url.charAt(0) == "/" && url.substr(0, ui.context.length) != ui.context){
            url = ui.context + url;
        }
        return url;
    }

    /**
     * <p>URL</p>
     * @namespace wui
     * @class getQueryString
     * @constructor 
     * @return {Object} [ URL ]
     * @example
     *  
     *   js: 
     *   
            // ,URL"http://xxx/xx.html?id=0102"
            var returnValue = wui.getQueryString();   // {id: "0102"}
     *
     */
    ui.getQueryString = function(url, isCodeUrlValue) {
        ui.logUtilConfig.debugLog.methodName = "getQueryString";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("getQueryString", FRAME_NAME);
        
        var urlString;
        if(url && typeof(url) === "string"){
            if(url.indexOf("?") > -1){
                urlString = url.substring(url.indexOf("?") + 1);
            }else{
                return {};
            }
            
        }else{
            if (window.location.search == "") {
                return {};
            }else{
                urlString = window.location.search.substring(1);
            }
        }
        
        var q = urlString.split("&");
        var returnValue = {};
        for (var i = 0; i < q.length; i++) {
            
            var temp = q[i].split("=");
            returnValue[temp[0]] = ui.decodeURLParamValue(temp[1], isCodeUrlValue);
        }
        
        return returnValue;
    }

    /**
     * <p>URL</p>
     * @namespace wui
     * @class getQueryStringByName
     * @constructor 
     * @param name {string} 
     * @return {string} [  ]
     * @example
     *  
     *   js: 
     *   
            // ,URL"http://xxx/xx.html?id=0102"
            var id = wui.getQueryStringByName("id");   // "0102"
     *
     */
    ui.getQueryStringByName = function(name, isCodeUrlValue) {
        ui.logUtilConfig.debugLog.methodName = "getQueryStringByName";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("getQueryStringByName", FRAME_NAME);
        // if(ui.isUndefined(isCodeUrlValue)){
        //     isCodeUrlValue = ui.CONSTANTS.ISCODEURLVALUE;
        // }

        var result = location.search.match(new RegExp("[\?\&]" + name + "=([^\&]+)", "i"));
        if (result == null || result.length < 1) {
            return "";
        }

        return ui.decodeURLParamValue(result[1], isCodeUrlValue);
    }

    

    return ui;
})(wui || {}, libs);