
;(function (ui,$, undefined){
	/**
     * <p>()</p>
     * <p></p>
     * <ol>
     * <li>data-init-page=true: </li>
     * <li>data-init-skin=true: </li>
     * <li>data-add-skin=true: ,</li>
     * <li>data-init-element=true: </li>
     * <li>data-init-components=true: </li>
     * <li>data-init-validate=true: </li>
     * <li>data-init-scroll=true: </li>
     * </ol>
     * <strong>,75</strong>
     * <ol>
     * <li>"data-init-page",</li>
     * <li>"data-init-page",: isInitPage() > "data-init-page"() > wui.isInitPage</li>
     * <li>"data-init-page", data-*-* ,,"data-init-page"</li>
     * <li>"data-init-page", data-*-* ,,"data-init-page"</li>
     * <li>"data-add-skin",wui.skinConfig.isAddSkin</li>
     * </ol>
     * @namespace wui
     * @class initPage
     * @constructor 
     * @param isInitPage {Boolean} ,: isInitPage() > "data-init-page"() > wui.isInitPage
     * @param setting {JSON} ,{"widgetId", widgetSetting, ...}
     * @return {JSON}  WUI,{"widgetId", widgetObject, ...},wui.extVariables.components
     * @example
     *  
     *   js: 
     *   
            wui.initPage();	// 
     *
     */
	ui.initPage = function(isInitPage, setting){
        ui.logUtilConfig.debugLog.methodName = "initPage";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

		ui.extVariables.components = {};

		var $initConSelector = null, dataIsInitPage;
		if($('[data-init-page]').length > 0){
			$initConSelector = $('[data-init-page]');
			if(typeof($initConSelector.attr("data-init-page")) === "undefined"){
	        	dataIsInitPage = ui.isInitPage;
	        }else{
	        	dataIsInitPage = $initConSelector.data("init-page");
	        }
		}else{
			$initConSelector = $('body');
			dataIsInitPage = ui.isInitPage;
			// $initConSelector.data("init-page", isInitPage);
		}

		if(ui.isUndefined(isInitPage)){
			isInitPage = dataIsInitPage;
		}else if(ui.isJson(isInitPage)){
			setting = isInitPage;
			isInitPage = dataIsInitPage;
		}

		ui.logParamValue({
            paramName: "isInitPage",
            paramValue: isInitPage, 
            methodName: "initPage", 
            className: FRAME_NAME, 
            isDebug: true
        });

		// 
		$initConSelector.data("data-init-page", isInitPage);

		// IE mode
	    // var isRTL = false;
	    // var isIE8 = false;
	    // var isIE9 = false;
	    // var isIE10 = false;
	    var invalidControls = "[type='button'],[type='submit'],[type='reset']";   // 

	    // 
	    var _initHandle = function() {

	        if ($('body').css('direction') === 'rtl') {
	            ui.isRTL = true;
	        }

	        if (ui.CONSTANTS.isIE10) {
	            $('html').addClass('ie10'); // detect IE10 version
	        }

	        if (ui.CONSTANTS.isIE10 || ui.CONSTANTS.isIE9 || ui.CONSTANTS.isIE8) {
	            $('html').addClass('ie'); // detect IE10 version
	        }

	        // 
	        // if(typeof($initConSelector.attr("data-init-page")) === "undefined"){
	        // 	$initConSelector.data("init-page", isInitPage);
	        // }

	        // var isInitPage = $initConSelector.data("init-page");

	        // 
	        if(typeof($initConSelector.attr("data-init-skin")) === "undefined"){
	        	$initConSelector.data("init-skin", isInitPage);
	        }

	        // ,
	        if(typeof($initConSelector.attr("data-add-skin")) === "undefined"){
	        	$initConSelector.data("add-skin", ui.skinConfig.isAddSkin);
	        }

	        // 
	        if(typeof($initConSelector.attr("data-init-element")) === "undefined"){
	        	$initConSelector.data("init-element", isInitPage);
	        }

	        // 
	        if(typeof($initConSelector.attr("data-init-components")) === "undefined"){
	        	$initConSelector.data("init-components", isInitPage);
	        }

	        // 
	        if(typeof($initConSelector.attr("data-init-validate")) === "undefined"){
	        	$initConSelector.data("init-validate", isInitPage);
	        }

	        // 
	        if(typeof($initConSelector.attr("data-init-scroll")) === "undefined"){
	        	$initConSelector.data("init-scroll", isInitPage);
	        }
	    };

	    // ,data-wui-scroll
	    var _initScroll = function(){
	    	
	    	if($initConSelector.data("initScroll") === false || $initConSelector.data("initScroll") === "false"){
	    		return false;
	    	}
	    	ui.scroll($initConSelector);
	    }

	    // 
	    var _initValidControls = function(){
	    	// console.info("_initValidControls");
	    	
	    	$("input:not(" + invalidControls + ")").each(function(i, elem){
	    		if(!$(elem).hasClass("wui-input") && !$(elem).attr("data-init-wui")){
	    			$(elem).addClass("wui-input");
	    		}
	    		if(!$(elem).attr("data-init-wui")){
	    			if($(elem).attr("id") && $(elem).attr("name") == undefined){
		    			$(elem).attr("name", $(elem).attr("id"));
		    		}
	    		}
		    		
	    	})

	    	$("select").each(function(i, elem){
	    		if(!$(elem).hasClass("wui-select")){
	    			$(elem).addClass("wui-select");
	    		}
	    		if(!$(elem).attr("data-init-wui")){
	    			if($(elem).attr("id") && $(elem).attr("name") == undefined){
		    			$(elem).attr("name", $(elem).attr("id"));
		    		}
	    		}
	    	})

	    	$("textarea").each(function(i, elem){
	    		if(!$(elem).hasClass("wui-textarea")){
	    			$(elem).addClass("wui-textarea");
	    		}
	    		if(!$(elem).attr("data-init-wui")){
	    			if($(elem).attr("id") && $(elem).attr("name") == undefined){
		    			$(elem).attr("name", $(elem).attr("id"));
		    		}
	    		}
	    	})
	    }

	    // 
	    var _initInvalidControls = function(){
	    	// console.info("_initInvalidControls");
	    	// 
	    	$initConSelector.find(invalidControls).each(function(i, elem){
	    		if(!$(elem).hasClass("wui-btn")){
	    			$(elem).addClass("wui-btn");
	    		}
	    	})
	    }

	    // UI
	    var _initUiWidget = function(initParams){
	    	if($initConSelector.data("initComponents") === false || $initConSelector.data("initComponents") === "false"){
	    		return false;
	    	}
	    	if(!ui.isJson(initParams)){
	    		initParams = {};
	    	}
	    	// initParams = initParams || {};
	    	// var widgets = {};
	        $initConSelector.find('[data-' + ui.CONSTANTS.DATAINITWIDGETFIELD + ']').each(function(i, elem){
	            var widgetName = $(elem).attr("data-" + ui.CONSTANTS.DATAINITWIDGETFIELD);
	            var widgetId = $(elem).attr("id");
	            if(widgetName && widgetId){
	                if(ui[widgetName]){
	                	var theInitParam = {};
	                	if(initParams[widgetId] != undefined){
	                		theInitParam = initParams[widgetId];
	                	}
	                    // widgetObject[widgetId] = ui[widgetName](widgetId, theInitParam);
	                    ui.extVariables.components[widgetId] = ui[widgetName](widgetId, theInitParam);
	                }else{
	                    ui.logWarn(widgetName + " ,:WUI!", FRAME_NAME+".initPage", true);
	                }
	            }
	        })
	    }

	    var _initElement = function(){
	    	if($initConSelector.data("initElement") === false || $initConSelector.data("initElement") === "false"){
	    		return false;
	    	}
	    	// :input()/select/textarea
	    	_initValidControls();

	    	// 
	    	_initInvalidControls();

	    }

	    // 
	    var _initValidate = function(){
	    	if($initConSelector.data("initValidate") === false || $initConSelector.data("initValidate") === "false"){
	    		return false;
	    	}
	    	$initConSelector.find("form").each(function(i, form){
	    		// 
    			if($(form).find('[' + ui.validateAttributeField + ']').length > 0){
		    		// ui.notice("validate");
		    		var formId = $(form).attr("id") || $(form).attr("name");
		    		if(formId){
		    			// widgetObject[formId] = wui.validate(form);
		    			ui.extVariables.components[formId] = wui.validate(form);
		    		}else{
		    			// widgetObject["formValidate"] = wui.validate(form);
		    			ui.extVariables.components["formValidate" + i] = wui.validate(form);
		    		}
		    	}else{
		    		// ui.notice("no");
		    	}
    		})
	    }

	    // 
	    var _initSkins = function(){
	    	if($initConSelector.data("initSkin") === false || $initConSelector.data("initSkin") === "false"){
	    		return false;
	    	}
	    	var isAddSkin = $initConSelector.data("addSkin");
	    	// console.error($initConSelector.data("initSkin"))
	    	ui.setSkins(null, isAddSkin);
	    }

	    // var _addRequiredStar = function(){
	    // 	if(ui.validateUtilConfig.isAddRequiredStar){
	    		
	    // 	}
	    // }

	    var _init = function(initWidgetParams){
	    	// 
	    	_initHandle();

	    	// 
	    	_initSkins();

	    	// 
	    	_initElement();

	    	// UI
	    	_initUiWidget(initWidgetParams);

	    	// 
	    	_initScroll();

	    	// 
	    	_initValidate();

	    }
	    _init(setting);

	    return ui.extVariables.components;
	};

	ui.initDesign = function(setting){

		ui.logParamValue({
            paramName: "setting",
            paramValue: setting, 
            methodName: "initDesign", 
            className: FRAME_NAME, 
            isDebug: true
        });

        var _initButton = function(){

        	var selector = "[type='button'],[type='submit'],[type='reset'],button,a";
        	$(selector).find(function(){
        		if($(this).attr("color")){
        			$(this).addClass($(this).attr("color"));
        		}
        		if($(this).attr("size")){
        			$(this).addClass($(this).attr("size"));
        		}
        		if($(this).attr("radius")){
        			$(this).addClass($(this).attr("radius"));
        		}
        		if($(this).attr("icon")){
        			$(this).prepend('<i class="fa fa-' + $(this).attr("icon") + '"></i>');
        		}
        		if($(this).attr("text")){
        			$(this).append($(this).attr("text"));
        		}
        	})
        }

	    var _init = function(initWidgetParams){
	    	// 
	    	_initButton();

	    }
	    _init(setting);
	}
	return ui;
})(wui || {}, libs);

// window .onload =function (){
// 	if(wui && wui.initPage && $('[data-init-page]').length > 0){
// 		// 
// 		wui.initPage();
// 	}
// }
jQuery(document).ready(function(){
	if(window.wui){
		var browser = wui.getBrowserVersion();
	    wui.logMessage("" + browser.browser + " " + browser.version, "wui.ready", true);
	    
	    wui.logMessage("WUI: " + wui.version + ", : " + wui.versionDate, "wui.ready", true);
	    // wui.setSkins(null, true);
	    
	    $('textarea[data-auto-height]').each(function(){
	    	wui.textarea(this);
	    })

	    // 
	    if(wui.isInitDesign){
	    	wui.initDesign();
	    }
	    
	    if(wui.isInitPage){
	    	// 
			wui.initPage();
	    }else if($('[data-init-page]').length > 0){
			// 
			wui.initPage();
		}else if(wui.skinConfig.isInitSkin){
			wui.setSkins();
		}
	}
})