;(function (ui, $, undefined){
    /*
     * 
     * @Author   Chenzx
     * @DateTime 2018-11-28
     * @param    {JSON}     obj       
     * @param    {JSON}     opt       
     * @param    {Object}   component 
     * @param    {Boolean}   isEnableRemove 
     */
    function TagNode(obj, opt, component, isEnableRemove){
        
        // 
        this.opt = opt;

        // 
        this.component = component;

        // 
        this.active = false;

        this.textFields = ui.getTextField(opt["text"]);

        // 
        // this.index = component.nodes.length;
        
        // 
        this.data = obj;
        if(typeof(obj) === "object"){
            this.item = obj;
            this.enable = ui.isUndefined(this.item["isEnableRemove"]) ? isEnableRemove : this.item["isEnableRemove"];
        }

        if(typeof(obj) === "string"){
            this.enable = isEnableRemove;
            this.item = {};
            this.item[opt["name"]] = obj;
            for(var _i = 0; _i < this.textFields.length; _i++){
                var jTextField = this.textFields[_i];
                this.item[jTextField.value] = obj;
            }
            // this.item[opt["text"]] = obj;
            this.item["isEnableRemove"] = this.enable;
        }

        // :"isEnableRemove" > isEnableRemove > component.isClosable
        this.enable = ui.isUndefined(this.enable) ? component.isClosable : this.enable;

        // key
        this.key = typeof(obj) === "string" ? obj : obj[opt["name"]];

        // 
        // this.tagTmp = '<span class="tag"><em></em><span data-role="remove"></span></span>';
        this.tagTmp = '<span class="tag label"><span wui-attr="title"></span><span data-role="remove"></span></span>';

        // 
        this.node = $(this.tagTmp);

        // 
        this.renderNode();


        this.component.itemsName.push(this.key);
    }

    TagNode.prototype = {
        constructor: TagNode,

        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-11-28
         * @return   {[type]}   [description]
         */
        renderNode: function(){
            var that = this, obj = this.data;

            // 
            this.renderTitle(this.item);

            this.addClass(this.opt.tagClass);

            this.component.$input ? this.component.$input.before(this.node) : this.component.$viewBox.append(this.node);

            // 
            this.nodeRenderStatus();

            this.node.on("click", function(event){
                event.stopPropagation();    // 
            });

            // this.node.keypress(function(event){
            //     var keynum = window.event ? event.keyCode : event.which;
            //     ui.notice(keynum)
            //     // if(keynum == ui.CONSTANTS.KEYS.ENTER || keynum == ui.CONSTANTS.KEYS.OTHER){
            //     //     ui.stopPropagation(event);  // 
            //     //     ui.preventDefault(event);
            //     // }
            // }).keyup(function(event){
            //     var keynum = window.event ? event.keyCode : event.which;
            //     ui.notice(keynum)
            //     if(keynum == ui.CONSTANTS.KEYS.ENTER || keynum == ui.CONSTANTS.KEYS.OTHER){
            //         that.enter();
            //     }
            // });

            this.node.children('[data-role="remove"]').on("click", function(event){
                // 
                event.stopPropagation();
                // 
                that.component.removeNode(that.data);
                // that.remove();
            })
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-10-28
         * @return   {[type]}   [description]
         */
        nodeRenderStatus: function(){

            this.setEnable(this.enable);

            this.component.trigger("nodeRender", this);
        },
        addClass: function(classNames){
            this.node.addClass(classNames);
        },
        removeClass: function(classNames){
            this.node.removeClass(classNames);
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-11-28
         * @return   {[type]}   [description]
         */
        remove: function(){
            // console.info(this.opt)
            var index = this.node.index();
            this.node.remove();
            this.component.nodes.splice(index, 1);
            this.component.itemsName.splice(index, 1);
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-11-29
         * @return   {[type]}   [description]
         */
        renderTitle: function(itemData){
            var that = this,
                text = this.opt.text,
                // 
                showTitle = text;

            if(ui.isUndefined(itemData)){
                itemData = this.item;
            }

            if(this.textFields.length > 0){
                for(var _i = 0; _i < this.textFields.length; _i++){
                    var jTextField = this.textFields[_i];
                    if(ui.isUndefined(itemData[jTextField.value])){
                        itemData[jTextField.value] = "";
                    }

                    showTitle = showTitle.replace(jTextField.match, itemData[jTextField.value]);
                }
            }else{
                // text,
                if(typeof(itemData) === "string"){
                    showTitle = itemData;
                }else{
                    showTitle = JSON.stringify(itemData);
                }
            } 

            // 
            this.title = that.normChars(showTitle);

            this.node.find('[wui-attr="title"]').each(function(i){
                $(this).html(that.title);
            })

            return that.title;
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-11-29
         * @return   {[type]}   [description]
         */
        normChars: function(title){
            var that = this,
                maxChars = this.opt.maxChars;
            
            if(maxChars > 0 && title.length > maxChars){
                title = title.substr(0, maxChars) + that.opt.ellipsis;
            }
            return title;
        },
        /*
         * 
         * @Author   Chenzx
         * @DateTime 2018-10-28
         * @param    {Boolean}  isEnable [description]
         */
        setEnable: function(isEnable){
            this.enable = isEnable === false ? false : true;
            if(this.enable && this.component.enable){
                this.node.children('[data-role="remove"]').removeClass('hide');
            }else{
                this.node.children('[data-role="remove"]').addClass('hide');
            }
        },
        isEnable: function(){
            return this.enable;
        },
        enter: function(){
            this.node.children('[data-role="remove"]').trigger("click");
        },
        focus: function(){
            this.node.focus();
        },
        blur: function(){
            this.node.blur();
        },
        setActive: function(isActive){
            if(isActive){
                this.addClass("selected");
            }else{
                this.removeClass("selected");
            }

            this.active = isActive ? true : false;
        },
        isActive: function(){
            return this.active;
        }
    }

    // 
    var _global = (function(){ return this || (0, eval)('this'); }());
    if (typeof module !== "undefined" && module.exports) {
        module.exports = TagNode;
    } else if (typeof define === "function" && define.amd) {
        define(function(){return TagNode;});
    } else {
        !('TagNode' in _global) && (_global.TagNode = TagNode);
    }

})( wui || {}, libs );