/**
 * @module Method
 */

 ;(function (ui, $, undefined) {

    /**
     * <p>(iframe)</p>
     * @namespace wui
     * @class setSkins
     * @constructor 
     * @param skinName {string} ,:"default"/
     * @param [onEnd] {Function} ,element:
     * @param [isAddSkin] {Boolean} ("#wuiSkin"),,false,,wui.skinConfig.isAddSkin
     * @param [win] {Object} window,window
     * @example
     *  
     *   js: 
     *   
            wui.setSkins("skins");
     *
     */
    ui.setSkins = function(skinName, isAddSkin, onDoThings, win){
        if(ui.isUndefined(isAddSkin)){
            isAddSkin = ui.skinConfig.isAddSkin;
        }
        // isAddSkin = isAddSkin || false;
        win = win || window;
        // console.info(win.document.readyState);
        if(win.document.readyState=="complete"){
            var iframes = win.document.getElementsByTagName("iframe");
            if(iframes && iframes.length > 0){
                for(var i = 0; i < iframes.length; i++){
                    ui.setSkins(skinName, isAddSkin, onDoThings, iframes[i].contentWindow);
                    // console.info(iframes[i].contentWindow.document.readyState)
                    // if(iframes[i].contentWindow.document.readyState=="complete"){
                    //     ui.setSkins(skinName, isAddSkin, onDoThings, iframes[i].contentWindow);
                    // }else{
                    //     iframes[i].onload = function(e){
                    //         ui.setSkins(skinName, isAddSkin, onDoThings, iframes[i].contentWindow);
                    //     }
                    // }
                }
            }
            
            if(win.wui){
                win.wui.setSkin(skinName, isAddSkin, onDoThings)
            }
        }else{
            // 300ms
            ui.setTimeout(function(){
                ui.setSkins(skinName, isAddSkin, onDoThings, win);
            }, 300)
        }
            
    }

    /**
     * <p>(iframe)</p>
     * @namespace wui
     * @class setSkin
     * @constructor 
     * @param [skinName] {string} ,:"default"/
     * @param [isAddSkin] {Boolean} ("#wuiSkin"),,false,,wui.skinConfig.isAddSkin
     * @param [onEnd] {Function} ,element:
     * @example
     *  
     *   js: 
     *   
            wui.setSkin("skins");
     *
     */
    ui.setSkin = function(skinName, isAddSkin, onEnd){
        ui.logUtilConfig.debugLog.methodName = "setSkin";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        ui.logMessage("" + skinName, "wui.setSkin", true);
        if(ui.isUndefined(isAddSkin)){
            isAddSkin = ui.skinConfig.isAddSkin;
        }

        // if(ui.isUndefined(skinName)){
        if(!skinName){
            skinName = ui.getLocalStorage(ui.CONSTANTS.SKINNAMEFIELD);
            if(!skinName){
                skinName = "default";
            }
        }

        var skinElement = ui.getSkinElement(isAddSkin ? skinName : false);
        if(skinElement){
            // day = day || 31;
            if(typeof(skinElement) === "string"){
                ui.setLocalStorage(ui.CONSTANTS.SKINNAMEFIELD, skinName);
            }else{
                var href = skinElement.attr("href");
                //  themes/skinName/style.css
                var currentSkinName = href.match(/themes\/(\S*)\//)[1];
                var basePath = href.substr(0, href.indexOf("themes")) + "themes/";
                if(currentSkinName !== skinName){
                    skinElement.attr("href", basePath + skinName + "/" + ui.skinConfig.skinFileName);
                    ui.setLocalStorage(ui.CONSTANTS.SKINNAMEFIELD, skinName);
                }
            }
        }
        onEnd && typeof(onEnd)==="function" && onEnd(skinElement);
        return skinElement ? true : false;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class removeSkin
     * @constructor 
     * @param [onEnd] {Function} ,element:
     * @example
     *  
     *   js: 
     *   
            wui.removeSkin("skins");
     *
     */
    ui.removeSkin = function(onEnd){
        ui.logUtilConfig.debugLog.methodName = "removeSkin";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        var skinElement = ui.getSkinElement();
        ui.deleteLocalStorage(ui.CONSTANTS.SKINNAMEFIELD);

        onEnd && typeof(onEnd)==="function" && onEnd(skinElement);
        return skinElement ? true : false;
    }

    /**
     * <p></p>
     * @namespace wui
     * @class getSkin
     * @constructor
     * @example
     *  
     *   js: 
     *   
            wui.getSkin();  // "default"
     *
     */
    ui.getSkin = function(){
        ui.logUtilConfig.debugLog.methodName = "getSkin";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        ui.getLocalStorage(ui.CONSTANTS.SKINNAMEFIELD);
    }

    /**
     * <p></p>
     * @namespace wui
     * @class getSkinElement
     * @constructor
     * @example
     *  
     *   js: 
     *   
            wui.getSkinElement();
     *
     */
    ui.getSkinElement = function(skinName){
        ui.logUtilConfig.debugLog.methodName = "getSkinFile";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        if(!$("#" + ui.skinConfig.skinId).exist()){
            ui.logMessage(" ID " + ui.skinConfig.skinId + " DOM", "wui.getSkinFile", true);
            if(skinName){
                var cssURL = ui.CONSTANTS.WUI_PROJECT_PATH + 'themes/' + skinName + '/' + ui.skinConfig.skinFileName;
                ui.logMessage("" + cssURL, true);

                var linkTag = $('<link href="' + cssURL + '" rel="stylesheet" type="text/css" id="' + ui.skinConfig.skinId + '" charset="utf-8" />');
                $($('head')[0]).append(linkTag);
                return skinName;
            }else{
                return false;
            }
        }

        if(document.getElementById(ui.skinConfig.skinId).tagName.toLowerCase() === "link"){
            return $("#" + ui.skinConfig.skinId);
        }else{
            ui.logMessage(" ID " + ui.skinConfig.skinId + " DOMlink", "wui.getSkinFile", true);
            return false;
        }
    }

    return ui;
})(wui || {}, libs);