    /**
     * <p></p>
     * @namespace wui
     * @class openModalDialog
     * @constructor 
     * @param options {json} 
     * @param options.url {string} Iframeurl
     * @param [options.successCallback] {function} (wui.closeModalDialog()),
     * @param [options.width] {string} ,'800px'
     * @param [options.height] {string} ,'460px'
     * @param [options.title] {string} ,
     * @param [options.isFull] {boolean} ,false
     * @param [options.onFull] {function} ,iframe
     * @param [options.isMaxmin] {boolean} ,
     * @param [options.closedCallback] {Function} ,,,,
     * @param [options.cancelCallback] {Function} ,,(index)DOM(layero),,return false
     * @param [options.offset] {String/Array} ,'auto' <br />
     *  <table class="table table-bordered table-condensed">
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                </tr> 
            </thead>
            <tbody>
                <tr>
                    <td>offset: 'auto'</td>
                    <td>,</td>
                </tr>
                <tr>
                    <td>offset: '100px'</td>
                    <td>top,</td>
                </tr>
                <tr>
                    <td>offset: ['100px', '50px']</td>
                    <td>topleft</td>
                </tr>
                <tr>
                    <td>offset: 't'</td>
                    <td></td>
                </tr>
                <tr>
                    <td>offset: 'r'</td>
                    <td></td>
                </tr>
                <tr>
                    <td>offset: 'b'</td>
                    <td></td>
                </tr>
                <tr>
                    <td>offset: 'l'</td>
                    <td></td>
                </tr>
                <tr>
                    <td>offset: 'lt'</td>
                    <td></td>
                </tr>
                <tr>
                    <td>offset: 'lb'</td>
                    <td></td>
                </tr>
                <tr>
                    <td>offset: 'rt'</td>
                    <td></td>
                </tr>
                <tr>
                    <td>offset: 'rb'</td>
                    <td></td>
                </tr>
            </tbody>
        </table>
     * @param [options.isShadeClose] {Boolean} ,false
     * @return {number} [  ]
     * @example
     *  
     *   js: 
     *
            var openPageUrl = "http://platform.depts.bingosoft.net/wui/index.html",
                _successCallback = function(serviceResult){
                    console.info(serviceResult);

                    if (serviceResult.isSuccess) {
                        wui.successNotice(serviceResult.message);
                    } else {
                        wui.errorNotice(serviceResult.message);
                    }
                },
                width = null,
                height = null,
                title = "",
                isFull = true;

            // 1()
            var index = wui.openModalDialog({
                url: openPageUrl,
                successCallback: _successCallback,
                // width: width,    // ,
                // height: height, // ,
                title: title,
                isFull: isFull
            });   // 0

            // 2:
            var index = wui.openModalDialog(openPageUrl, _successCallback, width, height, title, isFull);   // 0
     *
     */
    // ui.openModalDialog = function(url, successCallback, width, height, title, isFull, onFull, isMaxmin) {
    ui.openModalDialog = function(param) {
        ui.logUtilConfig.debugLog.methodName = "openModalDialog";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("openModalDialog", FRAME_NAME);

        
        var option = {}, params;
        // var url, successCallback, width, height, title, isFull, onFull, isMaxmin, closedCallback, cancelCallback, offset, isShadeClose;
        // console.info(param);
        

        if(typeof(param) === "string"){
            var args = Array.prototype.slice.call(arguments,1);
            // console.info(args);
            option.content = ui._getDefault("http://platform.depts.bingosoft.net/wui/", param);
            option.successCallback = args[0];
            option.width = ui._getDefault('800px', args[1]);
            option.height = ui._getDefault('460px', args[2]);
            option.title = ui._getDefault('', args[3]);
            option.isFull = ui._getDefault(false, args[4]);
            option.full = args[5];
            option.maxmin = ui._getDefault(false, args[6]);
            option.closedCallback = args[7];
            option.cancelCallback = args[8];
            option.offset = ui._getDefault("auto", args[9]);
            option.shadeClose = ui._getDefault(false, args[10]);

            option.area = [ option.width, option.height ];

            params = $.extend(true, {}, option);
        }else if(param && typeof(param) === "object"){
            option.content = ui._getDefault("http://platform.depts.bingosoft.net/wui/", param.url, param.content);
            option.successCallback = param.successCallback;
            option.width = ui._getDefault('800px', param.width);
            option.height = ui._getDefault('460px', param.height);
            option.title = ui._getDefault('', param.title);
            option.isFull = ui._getDefault(false, param.isFull);
            option.full = ui._getDefault(ui.CONSTANTS.EMPTYFUNC, param.onFull, param.full);
            option.maxmin = ui._getDefault(false, param.isMaxmin, param.maxmin);
            option.closedCallback = param.closedCallback;
            option.cancelCallback = param.cancelCallback;
            option.offset = ui._getDefault("auto", param.offset);
            option.shadeClose = ui._getDefault(false, param.isShadeClose, param.shadeClose);

            option.area = [ option.width, option.height ];
            // option.area = [ "96%", "96%"];

            params = $.extend(true, {}, option, param);
        }
        
        if(!params.end){
            params.end = function(){
                var modalDialog = ui.getModalDialogs().pop();
                if(ui.CONSTANTS.isIE){
                    $('[data-hide-opend-modal-ie]').each(function(i, elem){
                        // alert(i);
                        $(elem).removeClass("hide-opend-modal");
                    })
                }
                if(option.closedCallback && typeof(option.closedCallback) === "function"){
                    option.closedCallback()
                }
            }
        }

        // :(index)DOM(layero),,return false
        if(!params.cancel){
            params.cancel = function(index, layero){ 
                // wui.notice("cancel");
                var isClose = true;
                if(option.cancelCallback && typeof(option.cancelCallback) === "function"){
                    isClose = option.cancelCallback(index, layero)
                }

                if(isClose === false){
                    return false;
                }
            }
        }
        params.type = 2;

        if(ui.CONSTANTS.isIE){
            $('[data-hide-opend-modal-ie]').each(function(i, elem){
                $(elem).addClass("hide-opend-modal");
            })
        }
        
        ui.logParamValue({
            paramName: "params",
            paramValue: params, 
            methodName: "openModalDialog", 
            className: FRAME_NAME, 
            isDebug: true
        });
        // ui.logParamValue("openModalDialog.params", params, FRAME_NAME);
        var modalWindowIndex = ui.CONSTANTS.OMODALWINDOW.indexOf(ui.modalWindow);

        if(modalWindowIndex > -1){
            var index;
            for(var i = modalWindowIndex; i < ui.CONSTANTS.OMODALWINDOW.length; i++){
                var modalWindow = ui.CONSTANTS.OMODALWINDOW[i];
                var windowModal = window[modalWindow];
                // console.info(modalWindow);
                // console.info(windowModal);
                if(windowModal["layui"]){
                    windowModal["layui"].use('layer', function() {
                        layer = windowModal["layui"].layer;

                        index = layer.open(params);
                        ui.addModalDialog(index, params.successCallback);
                        
                        if(params.isFull){
                            // params.maxmin = false;
                            layer.full(index);  
                        }
                        return index;
                    });
                    break;
                }else if(windowModal["layer"]){

                    index = windowModal["layer"].open(params);
                    ui.addModalDialog(index, params.successCallback);
                    
                    if(params.isFull){
                        // params.maxmin = false;
                        windowModal["layer"].full(index);  
                    }
                    return index;
                }
            }
        }else{
            var index;
            if(typeof(ui.modalWindow) === "function"){
                var windowModal = ui.modalWindow.call(this, window);

                if(windowModal["layui"]){
                    windowModal["layui"].use('layer', function() {
                        layer = windowModal["layui"].layer;

                        index = layer.open(params);
                        ui.addModalDialog(index, params.successCallback);
                        
                        if(params.isFull){
                            // params.maxmin = false;
                            layer.full(index);  
                        }
                        return index;
                    });
                }else if(windowModal["layer"]){

                    index = windowModal["layer"].open(params);
                    ui.addModalDialog(index, params.successCallback);
                    
                    if(params.isFull){
                        // params.maxmin = false;
                        windowModal["layer"].full(index);  
                    }
                    return index;
                }
            }
        }
    }

    /*
     * <p></p>
     * @namespace wui
     * @class addModalDialog
     * @constructor 
     * @param index {int} 
     * @param [successCallback] {function} ,
     * @example
     *  
     *   js: 
     *   
            // 
            var index = wui.addModalDialog(0);
     *
     */
    ui.addModalDialog = function(index, successCallback){
        ui.logUtilConfig.debugLog.methodName = "addModalDialog";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("addModalDialog", FRAME_NAME);

        var modalDialog = {};
        modalDialog.modalDialogIndex = index;
        modalDialog.successCallback = function(res) {
            if (successCallback && typeof(successCallback) == 'function') {
                successCallback(res);
            }
        };

        // 
        ui.getModalDialogs().push(modalDialog);
    }

    /**
     * <p>( successCallback()closedCallback())</p>
     * @namespace wui
     * @class closeModalDialog
     * @constructor 
     * @param res {json} 
     * @example
     *  
     *   js: 
     *   
            // {"id": "1010101"},wui.openModalDialog()
            wui.closeModalDialog({"id": "1010101"});
     *
     */
    ui.closeModalDialog = function(res){
        ui.logUtilConfig.debugLog.methodName = "closeModalDialog";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        ui.logParamValue({
            paramName: "res",
            paramValue: res, 
            methodName: "closeModalDialog", 
            className: FRAME_NAME, 
            isDebug: true
        });
        // 
        // var modalDialog = ui.getModalDialogs().pop();
        var modalDialogs = ui.getModalDialogs();
        var modalDialog  = {};
        if(modalDialogs && modalDialogs.length > 0){
            modalDialog = modalDialogs[modalDialogs.length - 1];
        }
        if(modalDialog){
            modalDialog.successCallback(res);
            ui.dialogOff(modalDialog.modalDialogIndex);
            // alert("Closed");
        }
    }

    /**
     * <p>(closedCallback())</p>
     * @namespace wui
     * @class cancelModalDialog
     * @constructor 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.cancelModalDialog();
     *
     */
    ui.cancelModalDialog = function(){
        ui.logUtilConfig.debugLog.methodName = "cancelModalDialog";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("cancelModalDialog", FRAME_NAME);

        // 
        var modalDialogs = ui.getModalDialogs();
        var modalDialog;
        if(modalDialogs.length > 0){
            modalDialog = modalDialogs[modalDialogs.length - 1];
        }
        // var modalDialog = ui.getModalDialogs().pop();
        if(modalDialog){
            ui.dialogOff(modalDialog.modalDialogIndex);
        }
    }

    /*
     * window
     * @Author   Chenzx
     * @DateTime 2019-01-24
     * @return   {[type]}   [description]
     */
    ui.getWindowModal = function(){
        ui.logUtilConfig.debugLog.methodName = "getWindowModal";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("getModalDialogs", FRAME_NAME);
        
        var modalWindowIndex = ui.CONSTANTS.OMODALWINDOW.indexOf(ui.modalWindow);

        var windowModal = window.top;
        if(modalWindowIndex > -1){
            var modalWindow = ui.CONSTANTS.OMODALWINDOW[modalWindowIndex];
            windowModal = window[modalWindow];
        }else if(typeof(ui.modalWindow) === "function"){
            windowModal = ui.modalWindow.call(this, window);
        }

        return windowModal;
    }

    /*
     * <p></p>
     * @namespace wui
     * @class getModalDialogs
     * @constructor 
     * @param index {int} 
     * @example
     *  
     *   js: 
     *   
            // 
            var modalDialog = wui.getModalDialogs();
     *
     */
    ui.getModalDialogs = function(){
        ui.logUtilConfig.debugLog.methodName = "getModalDialogs";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("getModalDialogs", FRAME_NAME);
        
        // var modalWindowIndex = ui.CONSTANTS.OMODALWINDOW.indexOf(ui.modalWindow);

        var windowModal = ui.getWindowModal();
        // if(modalWindowIndex > -1){
        //     var modalWindow = ui.CONSTANTS.OMODALWINDOW[modalWindowIndex];
        //     windowModal = window[modalWindow];
        // }else{
        //     // var index;
        //     if(typeof(ui.modalWindow) === "function"){
        //         windowModal = ui.modalWindow.call(this, window);
        //     }
        // }
        if (windowModal && (!windowModal.modalDialogs || windowModal.modalDialogs.length == 0)) {
            windowModal.modalDialogs = [];
        }

        // if (!window.top.modalDialogs || window.top.modalDialogs.length == 0) {
        //     window.top.modalDialogs = [];
        // }
        
        ui.logParamValue({
            paramName: "modalDialogs",
            paramValue: windowModal.modalDialogs, 
            methodName: "getModalDialogs", 
            className: FRAME_NAME, 
            isDebug: true
        });
        // ui.logParamValue("getModalDialogs.modalDialogs",window.top.modalDialogs, FRAME_NAME)
        return windowModal.modalDialogs;
    }

    /*
     * <p></p>
     * @namespace wui
     * @class dialogOff
     * @constructor 
     * @param index {int} 
     * @example
     *  
     *   js: 
     *   
            // 
            wui.dialogOff(0);
     *
     */
    ui.dialogOff = function(dialogIndex){
        ui.logUtilConfig.debugLog.methodName = "dialogOff";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("dialogOff", FRAME_NAME);
        // //iframe
        // console.info(window.name)
        var index = parent.layer.getFrameIndex(window.name); //iframe
        // console.error("index:" + index);
        // console.error("dialogIndex:" + dialogIndex);
        dialogIndex = index;
        // parent.layer.close(index); //  
        
        var modalWindowIndex = ui.CONSTANTS.OMODALWINDOW.indexOf(ui.modalWindow);
        if(modalWindowIndex > -1){
            for(var i = modalWindowIndex; i < ui.CONSTANTS.OMODALWINDOW.length; i++){
                var modalWindow = ui.CONSTANTS.OMODALWINDOW[i];
                var windowModal = window.parent;
                if(modalWindow == "top"){
                    windowModal = window.top;
                }else if(modalWindow == "parent"){
                    if(window[modalWindow]["parent"]){
                        windowModal = window[modalWindow]["parent"];
                    }else{
                        windowModal = window.top;
                    }
                }
                if(windowModal["layui"]){
                    windowModal["layui"].use('layer', function() {
                        var layer = windowModal["layui"].layer;
                        layer.close(dialogIndex);
                        return true;
                    });
                }else if(windowModal["layer"]){
                    windowModal["layer"].close(dialogIndex);
                    return true;
                }
            }
        }else{
            if(typeof(ui.modalWindow) === "function"){
                var windowModal = ui.modalWindow.call(this, window);

                if(windowModal["layui"]){
                    windowModal["layui"].use('layer', function() {
                        var layer = windowModal["layui"].layer;
                        layer.close(dialogIndex);
                        return true;
                    });
                }else if(windowModal["layer"]){
                    windowModal["layer"].close(dialogIndex);
                    return true;
                }
            }
        }
    }

