    ui.deepCopy = function(json){
        ui.logUtilConfig.debugLog.methodName = "deepCopy";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("deepCopy", FRAME_NAME);
        
        if(typeof(json) !== "object"){
            ui.logMessage(" Object ,", "wui.deepCopy", true);
            return false;
        }
        var returnObject = $.extend(true, {}, json);    //,
        return returnObject; 
    }

