                this.fileSelectorId = null;

                // 
                this.hasDom = false;        // 
                this.initDom = this.$element.prop("outerHTML");
                this.defaults.clickable = "#" + this.element + " " + this.defaults.clickable;
                // 
                var AttributeParams = ui.getAttributeParams(this.$element, config);
                this._opt = $.extend(true, {}, config, options, AttributeParams);  // 

                this.isSupport = ui.CONSTANTS.ISSUPPORTH5;
                this._file = null;  // 
                this.files = [];    // Flash:,...
                this.filesFormMethod = [];  // 
                // this.    // 
                this.queuedFiles = [];  // 
