            /*
             * 
             * @Author   Chenzx
             * @DateTime 2018-10-30
             * @param    {[type]}   url [description]
             */
            setUploadUrl: function(url){
                if(!url){
                    url = this._opt.url;
                }
                if(!ui.isEmptyType(this._opt.otherParam)){
                    url = ui.buildUrl(url, this._opt.otherParam);
                }
                
                if(this.isSupport){
                    this._file.options.url = url;
                }else{
                    this._file.setUploadURL(url);
                }
            },
            /*
             * 
             * @Author   Chenzx
             * @DateTime 2018-10-30
             * @param    {[type]}   objectParams [description]
             */
            setPostParams: function(objectParams){
                if(this.isSupport){
                    this._file.options.params = objectParams;
                }else{
                    this._file.setPostParams(objectParams);
                }
            },
            /*
             * 
             * @Author   Chenzx
             * @DateTime 2018-10-30
             * @param    {[type]}   types       [description]
             * @param    {[type]}   description [description]
             */
            setFileTypes: function(types, description){
                
                if(this.isSupport){
                    this._file.options.acceptedFiles = types;
                    this._file.options.file_types_description = description;

                    if (types != null) {
                        this._file.hiddenFileInput.setAttribute("accept", types);
                    }else{
                        $(this._file.hiddenFileInput).removeAttr("accept");
                    }
                }else{
                    if(types == null){
                        types = "*.*";
                        description = description ? description : "All Files";
                    }else{
                        types = types.replace(/\./g, "\*.").replace(/,/g, ";");
                        description = description ? description : types;
                    }
                    this._file.setFileTypes(types, description);
                }
            },
            /*
             * ,MB
             * @Author   Chenzx
             * @DateTime 2018-10-30
             * @param    {[type]}   limitSize [description]
             */
            setFileSizeLimit: function(limitSize){
                if(this.isSupport){
                    if(!limitSize){
                        limitSize = 0;
                    }else if(typeof(limitSize) === "string"){
                        if(limitSize.toLowerCase().endWith('mb')){
                            limitSize = parseFloat(limitSize);
                        }else if(limitSize.toLowerCase().endWith('kb')){
                            limitSize = parseFloat(limitSize);
                            limitSize = Math.ceil(limitSize * 10000 / 1024) / 10000;
                        }else if(limitSize.toLowerCase().endWith('gb')){
                            limitSize = parseFloat(limitSize);
                            limitSize = limitSize * 1024;
                        }else if(limitSize.toLowerCase().endWith('b')){
                            limitSize = parseFloat(limitSize);
                            limitSize = Math.ceil(limitSize * 10000 * 10000 / 1024 / 1024) / 10000 / 10000;
                        }else{
                            limitSize = parseFloat(limitSize);
                        }
                    }
                    this._file.options.maxFilesize = limitSize;
                }else{

                    if(!limitSize){
                        limitSize = 0;
                    }else if(typeof(limitSize) === "nummber"){
                        limitSize = limitSize + 'MB';
                    }

                    this._file.setFileSizeLimit(limitSize);
                }
            },
            setFileUploadLimit: function(limitMaxFilesNumber){
                if(this.isSupport){
                    this._file.options.maxFiles = limitMaxFilesNumber;
                }else{
                    this._file.setFileUploadLimit(limitMaxFilesNumber);
                }
            },
            _reoption: function(newOptions){
                var params = this._opt,
                    isResetUrl = false;

                // url: {string} 
                if(newOptions.url != undefined && params.url != newOptions.url){
                    this._opt.url = newOptions.url;
                    isResetUrl = true;
                }

                // otherParam: {json} 
                if(newOptions.otherParam != undefined && params.otherParam != newOptions.otherParam){
                    this._opt.otherParam = newOptions.otherParam;
                    isResetUrl = true;
                }

                if(isResetUrl){
                    this.setUploadUrl();
                }

                // data: {json} 
                if(newOptions.data != undefined && params.data != newOptions.data){
                    this._opt.data = newOptions.data;
                    this.setPostParams(newOptions.data);
                }

                // acceptedFiles: {string} 
                if(newOptions.acceptedFiles != undefined && params.acceptedFiles != newOptions.acceptedFiles){
                    this._opt.acceptedFiles = newOptions.acceptedFiles;
                    this.setFileTypes(newOptions.acceptedFiles);
                }
                // maxFilesize: {string} ,MB
                if(newOptions.maxFilesize != undefined && params.maxFilesize != newOptions.maxFilesize){
                    this._opt.maxFilesize = newOptions.maxFilesize;
                    this.setFileSizeLimit(newOptions.maxFilesize);
                }
                // maxNumber: {string} 
                if(newOptions.maxNumber != undefined && params.maxNumber != newOptions.maxNumber){
                    this._opt.maxNumber = newOptions.maxNumber;
                    this.setFileUploadLimit(newOptions.maxNumber);
                }

                console.info(this)
            },
