/**
 * @module Method
 */

 ;(function (ui,$, undefined) {
    /**
     * <p>Cookie,Cookie,</p>
     * @namespace wui
     * @class setCookie
     * @constructor 
     * @param key {string} cookie
     * @param value {string} cookie
     * @param [time] {number} [Cookie77]
     * @example
     *  
     *   js: 
     *   
            // cookie
            wui.setCookie({"userId": "0102"});
     *
     */
    ui.setCookie = function(key, val, time){
        ui.logUtilConfig.debugLog.methodName = "setCookie";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("setCookie", FRAME_NAME);
        
        var expiresDays = (time == undefined) ? 7 : parseInt(time);    //daten
        var date = new Date(); //

        date.setTime(date.getTime()+expiresDays*24*3600*1000); //cookie
        document.cookie = key + "=" + escape(val) + ";expires=" + date.toGMTString();  //cookie
    }

    /**
     * <p>keyCookie</p>
     * @namespace wui
     * @class deleteCookie
     * @constructor 
     * @param key {string} cookie
     * @example
     *  
     *   js: 
     *   
            // 
            wui.deleteCookie("userId");
     *
     */
    ui.deleteCookie = function(key){
        ui.logUtilConfig.debugLog.methodName = "deleteCookie";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("deleteCookie", FRAME_NAME);
         //cookie
         var date = new Date(); //
         date.setTime(date.getTime() - 10000); //date
         document.cookie = key + "=" + escape("") +"; expires =" + date.toGMTString();//cookie
    }

    /**
     * <p>cookie</p>
     * @namespace wui
     * @class getCookie
     * @constructor 
     * @param key {string} cookie
     * @return {string} [ cookiefalse ]
     * @example
     *  
     *   js: 
     *   
            // 
            var url = wui.getCookie("userId");   // "0102"
     *
     */
    ui.getCookie = function(key){
        ui.logUtilConfig.debugLog.methodName = "getCookie";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("getCookie", FRAME_NAME);
        // cookie
        var strCookie = document.cookie;    // cookie
        var arrCookie = strCookie.split(";");   // cookie";"
        for(var i = 0, _len = arrCookie.length; i < _len; i++){
            var _cookie = arrCookie[i].split("=");
            if(_cookie[0] == key){
                return unescape(_cookie[1]);
            }
        }
        return false;
    }

    /**
     * <p>localStorage</p>
     * @namespace wui
     * @class getLocalStorage
     * @constructor 
     * @param key {string} localStorage
     * @example
     *  
     *   js: 
     *   
            // cookie
            wui.getLocalStorage("userId");    // "010202"
     *
     */
    ui.getLocalStorage = function(key){
        ui.logUtilConfig.debugLog.methodName = "getLocalStorage";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        if(typeof(Storage) !== "undefined"){
            //  localStorage  sessionStorage !
            return localStorage.getItem(key);
        } else {
            ui.logWarn(" web ", "wui.getLocalStorage", true);
            return false;
        }
    }

    /**
     * <p>localStorage,key,,</p>
     * @namespace wui
     * @class setLocalStorage
     * @constructor 
     * @param key {string} localStorage
     * @param value {string} localStorage
     * @example
     *  
     *   js: 
     *   
            // cookie
            wui.setLocalStorage("userId", "010202");    // true
     *
     */
    ui.setLocalStorage = function(key, value){
        ui.logUtilConfig.debugLog.methodName = "setLocalStorage";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        if(typeof(Storage) !== "undefined"){
            //  localStorage  sessionStorage !
            localStorage.setItem(key,value);
            return true;
        } else {
            ui.logWarn(" web ", "wui.setLocalStorage", true);
            return false;
        }
    }

    /**
     * <p>localStorage</p>
     * @namespace wui
     * @class deleteLocalStorage
     * @constructor 
     * @param key {string} localStorage,
     * @example
     *  
     *   js: 
     *   
            // cookie
            wui.deleteLocalStorage("userId");    // true
     *
     */
    ui.deleteLocalStorage = function(key){
        ui.logUtilConfig.debugLog.methodName = "deleteLocalStorage";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);

        if(typeof(Storage) !== "undefined"){
            //  localStorage  sessionStorage !
            if(ui.isUndefined(key)){
                localStorage.clear();
            }else{
                localStorage.removeItem(key);
            }
            return true;
        } else {
            ui.logWarn(" web ", "wui.deleteLocalStorage", true);
            return false;
        }
    }

    return ui;
})(wui || {}, libs);