    // ui.load = function(icon, param2) {
    ui.load = function(param1, param2) {
        ui.logUtilConfig.debugLog.methodName = "load";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("load", FRAME_NAME);
        var setting = ui.layerSetting, option = {}, icon = 1, params;

        if(ui.isJson(param1)){
            param2 = param1;
        }else{
            icon = (typeof(param1) === "number") ? param1 : 1;
        }

        if(param2 && typeof(param2) === "object"){
            option.skin = ui._getDefault(setting.skin, param2.skin);
            option.time = ui._getDefault(setting.time, param2.time);
            option.shadeClose = ui._getDefault(setting.isShadeClose, param2.isShadeClose, param2.shadeClose);
            option.offset = ui._getDefault(setting.offset, param2.offset);
            option.success = ui._getDefault(setting.onPoped, param2.onPoped, param2.success);
            option.end = ui._getDefault(setting.onClosed , param2.onClosed, param2.end);

            params = $.extend(true, {}, option, param2);
        }else{
            // option = $.extend(true, {}, option, setting);
            option = {
                skin: setting.skin,
                time: setting.time,
                shadeClose: setting.isShadeClose,
                // {String/Array} ,
                offset: setting.offset
            }

            params = $.extend(true, {}, option);
        }
        
        ui.logParamValue({
            paramName: "params",
            paramValue: params, 
            methodName: "load", 
            className: FRAME_NAME, 
            isDebug: true
        });

        return layer.load(icon, params);
    }

