        if(options && typeof(options) === "object"){
            option.skin = ui._getDefault(setting.skin, options.skin);
            option.time = ui._getDefault(setting.time, options.time);
            option.btn = ui._getDefault(setting.btn, options.btn);
            option.offset = ui._getDefault(setting.offset, options.offset);
            option.shadeClose = ui._getDefault(setting.isShadeClose, options.isShadeClose, options.shadeClose);
            option.title = ui._getDefault(setting.title, options.title);
            option.success = ui._getDefault(setting.onPoped, options.onPoped, options.success);
            option.end = ui._getDefault(setting.onClosed , options.onClosed, options.end);

            option.area = [ option.width, option.height ];

            params = $.extend(true, {}, option, options);
        }

        // options = options || {
