	        _parseText: function(json){
	        	var text = this._opt.text, showText = text;

	        	if(!text){	// text
		    		showText = JSON.stringify(json);
		    	}else{
		    		var reg = /\{\{(.+?)\}\}/g;
		        	var result = text.match(reg);
		        	if(result){
		        		var _len = result.length;
			        	for(var _i = 0; _i < _len; _i++){
			        		var field = /^\{\{(.+?)\}\}$/.exec(result[_i]);
			        		if(field.length > 1){
			        			showText = showText.replace(field[0], json[field[1]]);
			        		}
			        	}
		        	}else{
			    		var textFiled = text.split(","), _len = textFiled.length;
			    		if(_len < 2){
			    			showText = json[text]
			    		}else{
			    			var textJson = {};
				    		for(var _i = 0; _i < _len; _i++){
				    			var textTemp = textFiled[_i];
				    			textJson[textTemp] = json[textTemp];
				    		}
				    		showText = JSON.stringify(textJson);
			    		}
		        	}
		    	}

	        	return showText;
	        },
	        // return number/this 
