	        option: function(newOptions){
	        	var params = this._opt;
	        	// console.info(newOptions);
	        	if(!!newOptions){
	        		if(typeof newOptions === "object"){
	        			this._resetOption(newOptions);
	        		}else if(typeof newOptions === "string"){
	        			if(newOptions === "getDefOpt"){
	        				return config;
	        			}else{
	        				return params[newOptions] == undefined ? ui.logWarn("", FRAME_NAME + "." + _widgetName, true) : params[newOptions];
	        			}
	        		}
		        		
	        	}else{
	        		return params;
	        	}
	        },
	        _resetOption: function(newOptions){
	        	var params = this._opt,
	        		self = this,
	        		isClear = false,
	        		isRefreshDowndropPage = false;
	        	// console.info(newOptions)
	        	// direction: {string} ,,,dropup,
	        	if(newOptions.direction != undefined && params.direction != newOptions.direction){
	        		this._opt.direction = newOptions.direction;
                    this._setDirection();
	        	}

	        	if(newOptions.name != undefined && params.name != newOptions.name){
	        		isClear = true;
	        		this._opt.name = newOptions.name;
	        	}

	        	if(newOptions.text != undefined && params.text != newOptions.text){
	        		isClear = true;
	        		this._opt.text = newOptions.text;
	        	}

	        	if(isClear){
	        		this.empty();
	        	}

	        	// multipleSeparator: {char} 
	        	if(newOptions.multipleSeparator != undefined && params.multipleSeparator != newOptions.multipleSeparator){
	        		var itemsText = this.itemsText;
	        		if(itemsText.length > 0){
	        			this.$resultBox.text(itemsText.join(newOptions.multipleSeparator));
	        		}
	        		this._opt.multipleSeparator = newOptions.multipleSeparator;
	        	}

	        	// maxNumber: {number} 0
	        	if(newOptions.maxNumber != undefined && params.maxNumber != parseInt(newOptions.maxNumber)){
	        		var maxNumber = ui.formatMaxNumber(newOptions.maxNumber);
	        		this._opt.maxNumber = maxNumber;
	        		if(maxNumber != false){
	        			var len = this.itemsName.length;
	        			while(len > maxNumber){
	        				this._removeItem(--len, true);
	        			}
	        		}
	        	}

	        	// {string} json
	        	if(newOptions.otherParam != undefined && newOptions.otherParam != params.otherParam){
	        		isRefreshDowndropPage = true;
	        		this._opt.otherParam = newOptions.otherParam;
	        	}

	        	// {string} 
	        	if(newOptions.treeTitle != undefined && newOptions.treeTitle != params.treeTitle){
	        		isRefreshDowndropPage = true;
	        		this._opt.treeTitle = newOptions.treeTitle;
	        	}

	        	// {string} URL
	        	if(newOptions.treeUrl != undefined && newOptions.treeUrl != params.treeUrl){
	        		isRefreshDowndropPage = true;
	        		this._opt.treeUrl = newOptions.treeUrl;
	        	}

                // {string} 
                if(newOptions.lazyField != undefined && newOptions.lazyField != params.lazyField){
                    isRefreshDowndropPage = true;
                    this._opt.lazyField = newOptions.lazyField;
                }

                // {string} 
                if(newOptions.isEnabledSearch != undefined && newOptions.isEnabledSearch != params.isEnabledSearch){
                    isRefreshDowndropPage = true;
                    this._opt.isEnabledSearch = newOptions.isEnabledSearch;
                }

	        	// {string} Tab(",")
	        	if(newOptions.tabTitle != undefined && newOptions.tabTitle != params.tabTitle){
	        		isRefreshDowndropPage = true;
	        		this._opt.tabTitle = newOptions.tabTitle;
	        	}

	        	// {string} URL(",")
	        	if(newOptions.tabUrl != undefined && newOptions.tabUrl != params.tabUrl){
	        		isRefreshDowndropPage = true;
	        		this._opt.tabUrl = newOptions.tabUrl;
	        	}

	        	// {string} iframe
	        	if(newOptions.pageUrl && params.pageUrl  != newOptions.pageUrl){
	        		if(newOptions.pageUrl != ""){
	        			isRefreshDowndropPage = true;
	        			this._opt.pageUrl = newOptions.pageUrl;
	        		}else{
	        			ui.logError("", FRAME_NAME + "." + _widgetName, true);
	        			return false;
	        		}
	        	}

	        	// isReload
	        	if(newOptions.isReload != undefined && params.isReload != newOptions.isReload){
	        		this._opt.isReload = (newOptions.isReload || newOptions.isReload == "true")? true : false;
	        	}

	        	if(isRefreshDowndropPage){
	        		if(!this._opt.isReload){
	        			this._loadIframe();
	        		}
	        	}

	        },
