    /**
     * <p>data-init-wui,</p>
     * @namespace wui
     * @class init
     * @constructor
     * @param [initParams] {JSON} ,{id: , ...},:>js
     * @return {JSON} [{"widgetId": widgetObject,...},]
     * @example
     *  
     *   html():
     *  
            <input type="text" id="date1" />
            <input type="text" id="tagBox1" />
     *   js(): 
     *
            var oWidget = {};
            oWidget["date1"] = wui.date("date1", {
                "format": "yyyy-mm-dd hh"
            });

            oWidget["tagBox1"] = wui.tagBox("tagBox1", {
                "loadUrl": "/Server.aspx"
            })
     *    
     *   html():
     *  
            <input type="text" id="date1" data-init-wui="date" data-wui-format="yyyy-mm-dd hh" />
            <input type="text" id="tagBox1" data-init-wui="tagBox" data-wui-loadUrl="/Server.aspx" />  
     *   js(): 
     *   
            // 
            var oWidget = wui.init();   // oWidget
     *
     */
    ui.init = function(initParams){
        ui.logUtilConfig.debugLog.methodName = "init";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("init", FRAME_NAME);

        if(!ui.isJson(initParams)){
            initParams = {};
        }
        var widgets = {};
        $('[data-' + ui.CONSTANTS.DATAINITWIDGETFIELD + ']').each(function(i, elem){
            var widgetName = $(elem).attr("data-" + ui.CONSTANTS.DATAINITWIDGETFIELD);
            var widgetId = $(elem).attr("id");
            if(widgetName && widgetId){
                if(ui[widgetName]){
                    var theInitParam = {};
                    if(!ui.isUndefined(initParams[widgetId])){
                        theInitParam = initParams[widgetId];
                    }
                    widgets[widgetId] = ui[widgetName](widgetId, theInitParam);
                }else{
                    ui.logWarn(widgetName + " ,:WUI!", FRAME_NAME+".init", true);
                }
            }
        })

        // if($('[' + ui.validateAttributeField + ']').length > 0){
        //     ui.validate();
        // }

        return widgets;
    }

    /**
     * <p>data-wui-*</p>
     * @namespace wui
     * @class getAttributeParams
     * @constructor 
     * @param $element {String} jQuery
     * @param config {Object} 
     * @param [DATAATTRIBUTEFIELD] {String} [,"wui"(data-wui-paramName=paramValue)]
     * @return {JSON} 
     * @example
     *
     *   html
     *  
            <input type="text" id="date" data-wui-isShowBtnGroup=false />
     *   js: 
     *
            wui.getAttributeParams($("#date", {"isShowBtnGroup"})); // {"isShowBtnGroup":false} 
            //  config,{"isshowbtngroup":false}
     *
     */
    ui.getAttributeParams = function($element, config, dataAttributeField){
        config = config || {};
        dataAttributeField = dataAttributeField || ui.CONSTANTS.DATAATTRIBUTEFIELD;   // "wui"
        var datas = $element.data(),
            params = {};
        // console.info(dataAttributeField);
        for(var attr in datas){
            // "wui",
            if(attr.toLowerCase().indexOf(dataAttributeField) == 0){
                var paramName = attr.toLowerCase().substring(dataAttributeField.length, attr.length);
                if(ui.isArray(config)){
                    for(var i = 0; i < config.length; i++){
                        if(config[i].toLowerCase() == paramName){
                            paramName = config[i];
                            break;
                        }
                    }

                    params[paramName] = datas[attr];
                }else if(ui.isJson(config)){
                    for(var key in config){
                        if(key.toLowerCase() == paramName){
                            paramName = key;
                            break;
                        }
                    }

                    var value = datas[attr];
                    switch(typeof(config[paramName])){
                        case 'boolean':
                            value = ui.boolean(datas[attr]);
                            break;
                        case 'number':
                            value = ui.number(datas[attr]);
                            break;
                        case 'object':
                            value = ui.parseToObj(datas[attr]);
                            break;

                    }

                    params[paramName] = value;
                }
            }
        }
        // console.info(params);
        return params;
    }

    ///**
     //* <p></p>
     //* @namespace wui
     //* @class checkIsLoadPlugins
     //* @constructor 
     //* @param widgetName {String} 
     //* @return {Boolean} 
     //* @example
     //*  
     //*   js: 
     //*   
     //       wui.checkIsLoadPlugins("date"); // datetimepickertruefalse
     //*
     //*/
    ui.checkIsLoadPlugins = function(widgetName){
        ui.logUtilConfig.debugLog.methodName = "checkIsLoadPlugins";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("checkIsLoadPlugins", FRAME_NAME);
        var isLoadPlugin = true;
        if(widgetName){
            var element = $('<div></div>').appendTo('body');
            switch(widgetName){
                case "date":
                    try{
                        if(!$.fn.datetimepicker){
                            ui.logWarn(" date :datetimepicker", FRAME_NAME+".checkIsLoadPlugins", true);
                            isLoadPlugin= false;
                        }
                    }catch(err){
                        ui.logWarn(" date ", FRAME_NAME+".checkIsLoadPlugins", true);
                        isLoadPlugin = false;
                    }finally{
                        break;
                    }
                case "file":
                    try{
                        // 
                        if(typeof(Dropzone) == "undefined"){
                            ui.logWarn(" file :Dropzone", FRAME_NAME+".checkIsLoadPlugins", true);
                            isLoadPlugin = false;
                        }

                        if(typeof(SWFUpload) == "undefined"){
                            ui.logWarn(" file :SWFUpload,IE8", FRAME_NAME+".checkIsLoadPlugins", true);
                            isLoadPlugin = false;
                        }
                    }catch(err){
                        // console.info(err);
                        ui.logWarn(" file :DropzoneSWFUpload", FRAME_NAME+".checkIsLoadPlugins", true);
                        isLoadPlugin = false;
                    }finally{
                        break;
                    }
                case "tree":
                    try{
                        if(!($.ui)){
                            ui.logWarn(" tree :jquery-ui", FRAME_NAME+".checkIsLoadPlugins", true);
                            isLoadPlugin = false;
                        }else if(!$.ui.fancytree){
                            ui.logWarn(" tree :fancytree", FRAME_NAME+".checkIsLoadPlugins", true);
                            isLoadPlugin = false;
                        }
                    }catch(err){
                        ui.logWarn(" tree :jquery-uifancytree", FRAME_NAME+".checkIsLoadPlugins", true);
                        isLoadPlugin = false;
                    }finally{
                        break;
                    }
                case "grid":
                    try{
                        if(!($.fn.jqGrid)){
                            ui.logWarn(" grid :jqGrid", FRAME_NAME+".checkIsLoadPlugins", true);
                            isLoadPlugin = false;
                        }
                    }catch(err){
                        ui.logWarn(" grid ", FRAME_NAME+".checkIsLoadPlugins", true);
                        isLoadPlugin = false;
                    }finally{
                        break;
                    }
                case "img":
                    try{
                        // 
                        if(typeof(Dropzone) == "undefined"){
                            ui.logWarn(" img :Dropzone", FRAME_NAME+".checkIsLoadPlugins", true);
                            isLoadPlugin = false;
                        }

                        if(typeof(SWFUpload) == "undefined"){
                            ui.logWarn(" img :SWFUpload,IE8", FRAME_NAME+".checkIsLoadPlugins", true);
                            isLoadPlugin = false;
                        }
                    }catch(err){
                        ui.logWarn(" img :DropzoneSWFUpload", FRAME_NAME+".checkIsLoadPlugins", true);
                        isLoadPlugin = false;
                    }finally{
                        break;
                    }
                default:
                    // ui.logMessage("");
                    break;
            }
            
        }

        return isLoadPlugin;
    }

