     //          title : '',
     //          type : 'button'
     //      }, "after");
     //
     ///
    ui.addToolBar = function(obj, btnContents, nodePosition){
        ui.logUtilConfig.debugLog.methodName = "addToolBar";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("addToolBar", FRAME_NAME);

        nodePosition = nodePosition || 'after';
        var $btnToolBar = $('<span class="input-group-btn" wui-attr="toolBar" ui-control="toolBar"></span>');

        if(obj instanceof jQuery){ 
            // alert("jQuery"); 
            obj[nodePosition]($btnToolBar);
        }else if(typeof obj === "string"){
            // obj = $("#" + obj);
            $("#" + obj)[nodePosition]($btnToolBar);
        } else{
            return;
        }
        

        $.each(btnContents, function(index, oBtn){
            var cssClass = oBtn.cssClass || "wui-btn-white";
            var type = oBtn.type || "button";
            var $btn = $('<button class="wui-btn ' + cssClass + '" wui-toolBar-action="' + oBtn.action + '" ui-toolBar-action="' + oBtn.action + '" type="' + type + '" title="' + oBtn.title + '">' + oBtn.html + '</button>');

            $btnToolBar.append($btn);
        })
        return true;
    }

    ///**
     //* <p></p>
     //* @namespace wui
     //* @class mask
     //* @constructor 
     //* @param isShow {Boolean} 
     //* @param opacityNum {int} 
     //* @example
     //*  
     //*   js: 
     //*   
     //       // 
     //       wui.mask();
     //*
     //*/
    ui.mask = function (isShow, opacityNum){
        ui.logUtilConfig.debugLog.methodName = "mask";
        ui.logMethodCalled(ui.logUtilConfig.debugLog);
        // ui.logMethodCalled("mask", FRAME_NAME);

        isShow = isShow || false;
        opacityNum = parseInt(opacityNum) || 1;
        var maskHtml = '<div class="ui-mask"></div>';
        if(isShow){
            if($(".ui-mask").length > 0){
                $(".ui-mask").show();           
            }else{
                $('body').append(maskHtml); 
            }
        
        }else{
            $(".ui-mask").hide();
        }
        if(opacityNum){
            $(".ui-mask").attr("opacity", opacityNum);          
        }
     }

